<template>
    <section id="applet" @mousemove="changeLogHeight" @mouseleave="mouseLeave">
        <div class="code" id="code2" ref="code">
            <div class="top">
                <div class="top1">
                    <div class="text">
                        <span style="white-space: nowrap;">HTMLJS</span>
                        <!-- <span style="white-space: nowrap;">{{codeName}}</span> -->
                    </div>
                    <div class="action">
                        <!-- <img class="run" :src="require('@/assets/img/runClick.png')" v-if="runActive" @click="endScript"> -->
                        <!-- <img class="set" :src="setimg1" @click="setShow" v-if="drawer === false" title="">
                        <img class="set" :src="setimg2" @click="setShow" v-if="drawer === true" title=""> -->
                        <img class="run" :src="runimg" @click="runScript" @mouseover="mouseOver" @mouseleave="runimg=runimg1" title="">
                        <img class="save" :src="require('@/assets/img/saveClick.png')" v-if="saveActive" @click="saveScript"  title="">
                        <img class="save" :src="require('@/assets/img/save.png')" v-else @click="notSave"  title="">
                        <img class="runPro" :src="runPro" @click="runProScript" @mouseover="runPro=runPro2" @mouseleave="runPro=runPro1"  title="">
                    </div>
                </div>
                <div ref="tabs">
                <el-tabs v-model="activeFile" type="card" @edit="handleTabsEdit" closable>
                    <el-tab-pane v-for="item in appLet.codeData" :label="item.name" :key="item.folderId" :name="item.folderId">
                    </el-tab-pane>
                </el-tabs>
                </div>
            </div>
            <div class="codeBox" ref="codemirror">
                <codemirror v-show="item.folderId===activeFile" v-for="(item, index) in appLet.codeData" :key="item.folderId" :idx="index"></codemirror>
            </div>
            <div class="runMenu" v-if="runActive">
                <div v-for="(item, index) in codeName" :key="index" class="runFile">
                    <el-row>
                        <div class="runFileBox" @click="runFile(item, index)">
                            <!-- {{codeData[index].icon}} -->
                            <div style="width:15%"><img :src="codeData[index].icon"></div>
                            <div style="width:85%" class="title"><span>{{item}}</span></div>
                        </div>
                    </el-row>
                </div>
            </div>
            <div class="runMenu" v-if="saveActive">
                <div class="runFile">
                    <el-row>
                        <div class="runFileBox" @click="save(0)">
                            <div><span></span></div>
                        </div>
                        <div class="runFileBox" @click="save(1)">
                            <div><span></span></div>
                        </div>
                    </el-row>
                </div>
            </div>
        </div>
        <log class="log" ref="log"></log>
        <div v-if="selectPage === '2'">
        <el-drawer
            title="!"
            :visible.sync="drawer"
            direction="rtl"
            :modal="false"
            :append-to-body="false"
            @opened="appSetUpdata"
            size="600px">
            <div slot="title" style="display:flex;">
                <img style="height:20px;width:26px;" :src="require('@/assets/img/logo.png')">
                <div style="line-height:20px;margin-left:10px;">{{drawerTitle}}</div>
            </div>
            <appLetSet ref="appletSet" @cacel="drawer=false" :appSetData="appSetData"></appLetSet>
        </el-drawer>
        </div>
    </section>
</template>
<script>
import Bus from '@/bus'
import log from '@/components/log'
import {mapState} from 'vuex'
import codemirror from '@/components/codemirror2'
import appLetSet from '@/components/appLetSet'
export default {
    name: 'aloneFileDebug',
    components: {
        codemirror,
        log,
        appLetSet
    },
    computed: {
        ...mapState(['appLet', 'selectPage'])
    },
    data () {
        return {
            editor: null,
            codes: 'echo 1',
            taskContent: '',
            dayProjectName: 'zz',
            dayJobName: 'test1',
            item: {
                code: ''
            },
            // 
            runimg: {},
            runimg1: require('@/assets/img/run.png'),
            runimg2: require('@/assets/img/runClick.png'),
            runPro: {},
            runPro1: require('@/assets/img/runPro.png'),
            runPro2: require('@/assets/img/runProClick.png'),
            setimg: {},
            setimg1: require('@/assets/img/set.png'),
            setimg2: require('@/assets/img/setClick.png'),
            drawer: false,
            proJect: '',
            jobNames: '',
            // 
            activeFile: '-1',
            runActive: false,
            runProActive: false,
            saveActive: false,
            listenFn: {},
            // 
            selectFileData: {},
            drawerTitle: '',
            appSetData: {}
        }
    },
    watch: {
        selectPage (newValue, oldValue) {
            this.drawer = false
        },
        activeFile (newValue, oldValue) {
            Bus.$emit('page2Select', newValue)
        }
    },
    created () {
        this.runimg = this.runimg1
        this.runPro = this.runPro1
        this.setimg = this.setimg1
        Bus.$on('openFile2', (data) => {
            this.$store.commit('setAppCodeData', data)
            this.activeFile = data.folderId
        })
        Bus.$on('appLetAttr', (data) => {
            this.drawerTitle = data.name
            this.drawer = true
            this.appSetData = data
        })
        // run
        let that = this
        this.listenFn = function setRunActive (e) {
            if (e.target.className !== 'save' && e.target.className !== 'runFileBox') {
                // console.log(e.target.className)
                that.saveActive = false
            }
        }
        document.addEventListener('click', this.listenFn)
    },
    methods: {
        // 
        runScript () {
            let folderId = this.activeFile
            let index = this.appLet.codeFolderId.indexOf(folderId)
            if (folderId !== -1) {
                this.runFile(folderId, index)
            }
        },
        saveScript () {
            this.saveActive = false
        },
        notSave () {
            this.saveActive = true
        },
        save (type) {
            // 0
            // 1
            this.saveActive = false
            let oData = {}
            if (type === 0) {
                let idx = this.appLet.codeFolderId.indexOf(this.activeFile)
                let spaceId = this.appLet.codeData[idx].spaceId
                oData = {
                    spaceId: spaceId,
                    folderId: '',
                    change: {}
                }
                oData.change[this.appLet.codeData[idx].folderId] = this.appLet.codeFile[this.appLet.codeData[idx].folderId]
                this.http.runHtmlPage(oData).then(res => {
                    if (res.status === 200) {
                        let temp = {
                            folderId: this.appLet.codeData[idx].folderId,
                            data: this.appLet.codeFile[this.appLet.codeData[idx].folderId]
                        }
                        this.$store.commit('setAppCodeFileSave', temp)
                        this.$message({type: 'success', message: ''})
                    }
                }).catch(err => {
                    return err
                })
            } else if (type === 1) {
                let idx = this.appLet.codeFolderId.indexOf(this.activeFile)
                let spaceId = this.appLet.codeData[idx].spaceId
                oData = {
                    spaceId: spaceId,
                    folderId: '',
                    change: this.appLet.codeFile
                }
                this.http.runHtmlPage(oData).then(res => {
                    if (res.status === 200) {
                        this.$store.commit('setAppCodeFileSave2')
                        this.$message({type: 'success', message: ''})
                    }
                }).catch(err => {
                    return err
                })
            }
        },
        handleTabsEdit (targetName, action) {
            this.$store.commit('removeAppCode', targetName)
            if (this.activeFile === targetName) {
                // console.log(this.appLet.codeData)
                if (this.appLet.codeData[0]) {
                    this.activeFile = this.appLet.codeData[0].folderId
                }
            }
        },
        // 
        runFile (name, index) {
            // vuex
            if (index === -1) {
                return false
            }
            let folderID = this.appLet.codeData[index].folderId
            let spaceId = this.appLet.codeData[index].spaceId
            name = this.appLet.codeData[index].name
            // let fileContent = this.codeFile[folderID]
            // 
            let fileTypeArr = name.split('.')
            let fileType = fileTypeArr[fileTypeArr.length - 1]
            if (fileType === 'html' || fileType === 'xml') {
                let oData = {
                    spaceId: spaceId,
                    folderId: folderID,
                    change: this.appLet.codeFile
                }
                this.http.runHtmlPage(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$message({type: 'success', message: 'HTML'})
                        let temp = {
                            folderId: folderID,
                            data: this.appLet.codeFile[folderID]
                        }
                        this.$store.commit('setCodeFileSave', temp)
                    }
                }).catch(err => {
                    return err
                })
            } else if (fileType === 'js') {
                let oData = {
                    spaceId: spaceId,
                    folderId: folderID,
                    change: this.appLet.codeFile
                }
                this.http.runJsCode(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$message({type: 'success', message: 'js'})
                        let temp = {
                            folderId: folderID,
                            data: this.appLet.codeFile[folderID]
                        }
                        this.$store.commit('setAppCodeFileSave', temp)
                    }
                }).catch(err => {
                    return err
                })
            }
        },
        // 
        runProScript () {
            let folderId = this.activeFile
            let index = this.appLet.codeFolderId.indexOf(folderId)
            if (folderId !== -1) {
                this.runAppPro(folderId, index)
            }
        },
        runAppPro (name, index) {
            // vuex
            if (index === -1) {
                return false
            }
            let folderID = this.appLet.codeData[index].folderId
            let spaceId = this.appLet.codeData[index].spaceId
            // let fileContent = this.codeFile[folderID]
            // 
            let oData = {
                spaceId: spaceId,
                folderId: '/',
                change: this.appLet.codeFile
            }
            this.http.runHtmlPage(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.$message({type: 'success', message: 'HTML'})
                    let temp = {
                        folderId: folderID,
                        data: this.appLet.codeFile[folderID]
                    }
                    this.$store.commit('setAppCodeFileSave', temp)
                }
            }).catch(err => {
                return err
            })
        },
        changeLogHeight () {
            if (this.$refs.log.changeHeightClicked) {
                this.$refs.log.changeHeight(window.event.clientY)
            }
        },
        mouseOver () {
            if (this.appLet.codeData[0]) {
                this.runimg = this.runimg2
            } else {
                return false
            }
        },
        mouseLeave () {
            this.$refs.log.changeHeightClicked = false
        },
        setShow () {
            this.drawer = true
        },
        handleClose () {

        },
        appSetUpdata () {
            // console.log('111')
            // this.$refs.appletSet.upData(this.appSetData)
        }
    },
    destroyed () {
        // let that = this
        this.drawer = false
        document.removeEventListener('click', this.listenFn, false)
        Bus.$off('openFile2')
        Bus.$off('appLetAttr')
    }
}
</script>

<style lang="scss" scoped>
#applet{
    overflow: hidden;
    height: 100%;
    width: 100%;
    color: #fff;
    text-align: left;
    position: relative;
    .top{
        height: 70px;
        background-color: #252525;
        border:1px solid rgba(77,77,77,1);
        .top1{
            height: 35px;
            font-size: 16px;
            border-bottom: 1px solid rgba(77,77,77,1);
            color: #ccc;
            position: relative;
            .text{
                width: calc(100% - 240px);
                overflow: hidden;
                text-overflow:ellipsis;
                white-space:nowrap;
                word-break:keep-all;
            }
            span{
                line-height: 35px;
                padding-left: 10px;
            }
            .set{
                position: absolute;
                right: 200px;
                top: 7px;
            }
            .run{
                position: absolute;
                right: 140px;
                top: 7px;
            }
            .runPro{
                position: absolute;
                right: 80px;
                top: 7px;
            }
            .save{
                position: absolute;
                right: 20px;
                top: 7px;
            }
        }
        .top2{
            height: 35px;
        }
    }
    #code2{
        height: calc(100% - 300px);
    }
    .log{
        height:300px;
    }
    .tab{
        // .icon{
        //     width: 30%;
        // }
        // .name{
        //     width: 70%;
        // }
    }
    .codeBox{
        width: 100%;
        height: calc(100% - 71px);
        background-color: #1D1F20;
        border-left: 1px solid #4d4d4d;
        border-right: 1px solid #4d4d4d;
        position: relative;
    }
    .el-drawer__wrapper{
        width: 600px;
        position: absolute;
        left:calc(100% - 600px);
    }
}
.runMenu{
    position: absolute;
    width: 200px;
    z-index: 5;
    top: 75px;
    right: 0;
    background:rgba(37,37,37,1);
    box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 1);
    .runFile{
        // line-height: 30px;
        // padding: 0 10px;
        // height: 30px;
        .title{
            overflow:hidden;
            text-overflow:ellipsis;
            white-space:nowrap;
            word-break:keep-all;
        }
        .runFileBox{
            display: flex;
            height: 30px;
            line-height: 30px;
            padding: 0 10px;
            cursor: pointer;
            // background-color: #fff;
        }
        .runFileBox:hover{
            background-color: #555;
        }
    }
}
</style>

<style lang="scss">
.el-drawer__wrapper{
    padding: 1px
}
.rtl{
    height: calc(100% - 36px);
    margin-top: 36px;
    background-color: #252525 !important;
    .el-drawer__header{
        color: #ccc;
    }
    box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 0.5) !important;
}
</style>



