<!--codeMirror-->
<template>
    <section id="codemirror">
        <textarea ref="mycode" class="codesql" v-model="code" style="width:100%;height:100%;">
            </textarea>
    </section>
</template>

<script>
import 'codemirror/theme/ambiance.css'
import 'codemirror/lib/codemirror.css'
import 'codemirror/addon/hint/show-hint.css'
import {mapState} from 'vuex'
let CodeMirror = require('codemirror/lib/codemirror')
require('codemirror/addon/edit/matchbrackets')
require('codemirror/addon/selection/active-line')
require('codemirror/mode/javascript/javascript.js')
require('codemirror/mode/sql/sql.js')
require('codemirror/mode/sql/sql')
require('codemirror/addon/hint/show-hint')
require('codemirror/addon/hint/sql-hint')
export default {
    name: 'codemirror',
    props: ['idx'],
    data () {
        return {
            editor: null,
            taskContent: '',
            dayProjectName: 'zz',
            dayJobName: 'test1',
            code: '',
            selectFileData: {}
        }
    },
    computed: {
        ...mapState(['selectPage', 'appLet'])
    },
    mounted () {
        this.start()
        this.selectFileData = this.appLet.codeData[this.idx]
        this.getFile(this.selectFileData)
    },
    methods: {
        // 
        start () {
            this.editor = CodeMirror.fromTextArea(this.$refs.mycode, {
                mode: 'sql',
                theme: 'ambiance',
                indentWithTabs: true,
                smartIndent: true,
                lineNumbers: true,
                matchBrackets: true,
                extraKeys: {'Ctrl': 'autocomplete'}, // 
                hintOptions: {// 
                    tables: {
                        users: ['name', 'score', 'birthDate'],
                        countries: ['name', 'population', 'size']
                    }
                }
            })
            setTimeout(() => {
                this.editor.on('cursorActivity', () => {
                    // console.log(this.editor.getValue())
                    this.code = this.editor.getValue()
                    let temp = {
                        folderId: this.appLet.codeData[this.idx].folderId,
                        data: this.code
                    }
                    this.$store.commit('setAppCodeFile', temp)
                    // this.editor.showHint()
                })
            }, 0)
        },
        // 
        getFile (data) {
            if (this.appLet.codeFile[data.folderId] === undefined || this.appLet.codeFile[data.folderId] === null) {
                let oData = {
                    spaceId: data.spaceId,
                    folderId: data.folderId
                }
                this.http.getFile(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        let temp = {
                            folderId: data.folderId,
                            spaceId: data.spaceId,
                            data: res.data
                        }
                        this.$store.commit('setAppCodeFileSave', temp)
                        this.$store.commit('setAppCodeFile', temp)
                        this.editor.setValue(res.data.toString())
                    }
                }).catch(err => {
                    return err
                })
            } else {
                this.editor.setValue(this.appLet.codeFile[data.folderId])
            }
        }
    }
}
</script>

<style lang="scss" scoped>
#codemirror{
    width: 100%;
    height: 100%;
    position: absolute;
    // top:0
}
</style>

<style lang="scss">
.CodeMirror{
    height: 100%;
    .CodeMirror-scroll{
        height: 100%;
    }
}
.CodeMirror-gutter{
    background-color: #1E1E1E;
    border:1px solid rgba(77,77,77,1);
}
.CodeMirror-linenumber{
    color: #ccc !important;
}
</style>



