<!--IDE-->
<template>
    <section id="top">
        <img :src="img1" @click="exitFun">
        <!-- <span></span>
        <span></span>
        <span></span> -->
        <el-tabs v-model="active">
            <el-tab-pane label="" name="1"></el-tab-pane>
            <el-tab-pane label="" name="2"></el-tab-pane>
            <el-tab-pane label="" name="3"></el-tab-pane>
        </el-tabs>
    </section>
</template>

<script>
import Bus from '@/bus'
export default {
    name: 'top',
    data () {
        return {
            img1: require('@/assets/img/logo.png'),
            active: '1'
        }
    },
    watch: {
        active: {
            immediate: true,
            handler: function (newValue, oldValue) {
                this.$store.commit('setSelectPage', newValue)
                // if (newValue === '1') {
                //     this.$router.push({name: 'aloneFileDebug'})
                // } else if (newValue === '2') {
                //     this.$router.push({name: 'applet'})
                // } else if (newValue === '3') {
                //     this.$router.push({name: 'share'})
                // }
            }
        }
    },
    methods: {
        exitFun () {
            Bus.$emit('exit', 0)
        }
    }
}
</script>

<style lang="scss" scoped>
#top{
    text-align: left;
    display: flex;
    height: 40px;
    background-color: #2F2F2F;
    cursor:pointer;
    img{
        height: 24px;
        width: 31px;
        margin-top: 7px;
        margin-left: 10px;
        margin-right: 10px;
    }
    span{
        line-height: 40px;
        margin-left: 10px;
        margin-right: 10px;
        color: #fff;
    }
}
</style>

<style lang="scss">
#top{
    .el-tabs__item{
        color: #fff;
    }
    .is-active{
        color: #fff;
    }
    .el-tabs__nav-wrap::after{
        background-color: #2F2F2F;
    }
}
</style>



