<!--IDE-->
<template>
    <section id="menu1">
        <Tree ref='tree' :treeData='treeData' :treeCheckBox='treeCheckBox' :treeExpandAll='treeExpandAll' :treeKeysId='treeKeysId'
           @treeNodeClick='treeNodeClick' @treeRename='treeRename' @treeNewfile='treeNewfile' @treeDelete='treeDelete' @treeNewTask='treeNewTask' pagetab="1"></Tree>
           <!-- <rightMenu ref="rightMenu" :menuShow="rightMenuShow" @newFolder="newFolder" @newFile="newFile"></rightMenu> -->
    </section>
</template>

<script>
import Tree from '@/components/tree3'
import rightMenu from '@/components/frame/rightMenu'
import {mapState} from 'vuex'
import Bus from '@/bus'
export default {
    name: 'menu1',
    components: {
        Tree,
        rightMenu
    },
    data () {
        return {
            treeData: [],
            treeCheckBox: false,
            treeExpandAll: false,
            treeKeysId: [],
            nodeData: {},
            historyMenu: -2,
            adjustMenu: -3,
            taskMenu: 3,
            listenFn: {},
            rightMenuShow: false,
            file: '',
            parId: ''
        }
    },
    created () {
        this.getData()
        Bus.$on('closeRight', () => {
            if (this.selectPage === '3') {
                setTimeout(() => {
                    this.rightMenuShow = false
                    this.hiddenMenu()
                }, 0)
            }
        })
        Bus.$on('closeRightMenu', () => {
            this.rightMenuShow = false
        })
        // Bus.$on('Event_getData', this.getData)
        Bus.$on('Event_File_data', this.treeFileData)
        Bus.$on('Event_treeDeleteData', this.treeDeleteData)
        Bus.$on('Event_ad_data', this.treeAmendData)
    },
    computed: {
        ...mapState(['selectPage', 'codeName', 'codeFolderId', 'codeData', 'codeFile', 'codeFileSave'])
    },
    destroyed () {
        Bus.$off('Event_File_data')
        Bus.$off('Event_treeDeleteData')
        Bus.$off('Event_ad_data')
    },
    watch: {

    },
    mounted () {

    },
    methods: {
        // 
        getData (data) {
            let oData = {
                spaceId: 'shareSpace'
            }
            this.http.shareGetFolders(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.treeData = res.data
                }
            }).catch(err => {
                return err
            })
        },
        // 
        getFolders () {
            let oData = {
                spaceId: 'shareSpace'
            }
            this.http.getFolders(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.treeData = res.data
                }
            }).catch(err => {
                return err
            })
        },
        treeNodeClick (data) {
            this.nodeData = data
        },
        treeRename (data, rename) {
            let oData = {
                spaceId: 'shareSpace',
                folderId: data.folderId,
                newName: rename
            }
            let oldFolderId = data.folderId
            let oldFolderIdArr = oldFolderId.split('/')
            let newFolderId = ''
            for (let i = 0; i < oldFolderIdArr.length - 1; i++) {
                newFolderId += oldFolderIdArr[i] + '/'
            }
            newFolderId += rename
            this.http.shareRenameFile(oData).then(res => {
                if (res.status === 200) {
                    // 
                    data.children = res.data.children
                    for (let key in data) {
                        if (key !== 'folderId') {
                            data[key] = res.data[key]
                        } else {
                            setTimeout(()=>{
                                data.folderId = res.data.folderId
                            },0)
                        }
                    }
                    this.$refs.tree.renameSuccess(data)
                    let folderId = {
                        new: newFolderId,
                        old: oldFolderId
                    }
                    this.$store.commit('fileRename', folderId)
                    Bus.$emit('event_modifyPath', rename, data.folderId, res.data)
                }
            }).catch(err => {
                return err
            })
        },
        treeNewfile (node, fileName, data) { // 
            let oData = {
                spaceId: 'shareSpace',
                parentId: node.parent.data.folderId,
                folderName: fileName,
                isFile: data.isFile,
                fileContent: ''
            }
            this.http.shareCreateFile(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.$refs.tree.newfileSuccess(res.data)
                    Bus.$emit('Event_leftTreeCreated', res.data, node.parent.data.folderId)
                }
            }).catch(err => {
                return err
            })
        },
        treeDelete (data) {
            // 
            let oData = {
                spaceId: 'shareSpace',
                folderId: data.folderId
            }
            this.http.shareDeleteFile(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    Bus.$emit('event_delFolder', data.folderId)
                    this.$refs.tree.dltSuccess()
                    this.$message({type: 'success', message: ''})
                }
            }).catch(err => {
                return err
            })
        },
        treeNewTask () {

        },
        hiddenMenu () {
            if (this.$refs.tree) {
                this.$refs.tree.hiddenMenu()
            }
        },
        treeFileData (data, path) { // 
            this.file = ''
            this.paths = ''
            this.parData = null
            this.file = 'addFile'
            this.paths = path
            this.parData = data
            this.getArrayList(this.treeData)
        },
        treeDeleteData (id) {
            this.file = ''
            this.parId = null
            this.file = 'delete'
            this.parId = id
            this.getArrayList(this.treeData)
        },
        treeAmendData (data) {
            this.file = ''
            this.parId = null
            this.file = 'amend'
            this.parId = data
            this.getArrayList(this.treeData)
        },
        getArrayList (data) { // 
        // data: 
            if (this.file == 'addFile') {
                if (this.paths.length == 1) {
                    data.push(this.parData)
                } else {
                    for (let i in data) { // 
                        if (data[i].folderId === this.paths[this.paths.length-1].folderId) {
                            // data[i].children.push(this.parData)
                            return
                        } else {
                            this.getArrayList(data[i].children)
                        }
                    }
                }
            } else if (this.file == 'delete') {
                for (let j in data) { // 
                    if (data[j].folderId === this.parId) {
                        data.splice(j, 1)
                        return
                    } else {
                        this.getArrayList(data[j].children)
                    }
                }
            } else if (this.file == 'amend') {
                for (let k in data) { // 
                    if (data[k].folderId === this.parId.folderId) {
                        var index = this.parId.folderId.lastIndexOf('\/')
                        this.parId.folderId = this.parId.folderId.substring(0, index)
                        data[k].name = this.parId.name
                        data[k].folderId = this.parId.folderId + '/' + this.parId.name
                        return
                    } else {
                        this.getArrayList(data[k].children)
                    }
                }
            }
        }
        // // 
        // rightClick (e) {
        //     this.$refs.tree.hiddenMenu()
        //     this.rightMenuShow = true
        //     this.$refs.rightMenu.setPosi(e.clientX, e.clientY)
        // },
        // // 
        // newFolder () {
        //     this.$refs.tree.newFolderBottom()
        // },
        // // 
        // newFile () {
        //     this.$refs.tree.newFileBottom()
        // }
    },
    destroyed () {
        // document.removeEventListener('click', this.listenFn, false)
        // Bus.$off(['Event_getData', this.getData])
        Bus.$off('Event_File_data')
    }
}
</script>

<style scoped>
#menu1{
    height: 100%;
    width: 100%;
    background-color: #252525;
    color: #fff;
    overflow-x: hidden;
    overflow-y: scroll;
}
.scrollbar{
    width: 30px;
    height: 300px;
    margin: 0 auto;

}
</style>

<style lang="scss" scope>
#menu1{
    .el-tree{
        background-color:transparent;
        color: #fff;
    }
    .el-tree-node:focus>.el-tree-node__content{
        background-color: #0E639C;
    }
    .el-tree-node__content:hover{
        background-color: #0E639C;
    }
}
</style>



