<!--IDE-->
<template>
    <div id="iframe" style="height:100%;position:relative;" @click="clickBody" @contextmenu.stop.prevent>
        <top></top>
        <div class="box">
            <div class="menu">
                <menu1 v-show="selectPage === '1'"></menu1>
                <menu2 v-show="selectPage === '2'"></menu2>
                <menu3 v-show="selectPage === '3'"></menu3>
            </div>
            <div class="view">
                <!-- <router-view ref="child"/> -->
                <aloneFileDebug v-show="selectPage === '1'"></aloneFileDebug>
                <applet v-show="selectPage === '2'"></applet>
                <share :isActive="selectPage === '3'" v-show="selectPage === '3'"></share>
            </div>
        </div>
        <!-- <el-dialog class="dlt-dialog" title="" :visible.sync="dialogDeleteVisible" width="400px">
            <span>&nbsp;{{deleteName}}&nbsp;</span>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="confirmDlt"> </el-button>
                <el-button @click="dialogDeleteVisible = false"> </el-button>
            </div>
        </el-dialog> -->
    </div>
</template>
<script>
import top from '@/components/frame/top'
import menu1 from '@/components/frame/menuDebug'
import menu2 from '@/components/frame/menuApplet'
import menu3 from '@/components/frame/menuShare'
import aloneFileDebug from '@/pages/aloneFileDebug'
import applet from '@/pages/applet'
import share from '@/pages/share'
import {mapState} from 'vuex'
import Bus from '@/bus'
export default {
    name: 'frame',
    components: {
        top,
        menu1,
        menu2,
        menu3,
        aloneFileDebug,
        applet,
        share,
        logIndex: 0
    },
    computed: {
        ...mapState(['selectPage'])
    },
    data () {
        return {
            dialogDeleteVisible: false,
            deleteName: {},
            logIndex: 0,
            t1: {}
        }
    },
    created () {
        Bus.$on('closeRight', () => {
            if (this.selectPage === '2') {
                setTimeout(() => {
                    // this.hiddenMenu()
                }, 0)
            }
        })
        Bus.$on('exit', this.ext)
        this.getHcServerAddress()
        // Bus.$on('deleteFile', (data) => {
        //     this.dialogDeleteVisible = true
        //     this.deleteName = data
        // })
        // setInterval(() => {
        //     this.getLog()
        // }, 2000)
    },
    mounted () {
        this.t1 = setInterval(() => {
            this.getLog()
        }, 1000)
        this.getAppId()
        this.getStorageInfo()
        this.isTokenVaildity()
    },
    methods: {
        ext (data) {
            this.lod = data
            localStorage.setItem('spaceMenu', data)
        },
        clickBody () {
            Bus.$emit('closeRight')
        },
        confirmDlt () {
            Bus.$emit('deleteFileBack', this.selectPage)
        },
        getLog () {
            let oData = {
                index: this.logIndex
            }
            this.http.getLog(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    // this.logs = res.data
                    this.logIndex += res.data.length
                    for (let i = 0; i < res.data.length; i++) {
                        this.$store.commit('setLogs', res.data[i])
                    }
                }
            }).catch(err => { return err })
        },
        // cloudAppID
        getAppId () {
            let oData = {}
            this.http.getAppId(oData).then(res => {
                // console.log(res)
            })
        },
        // IP
        getHcServerAddress () {
            let oData = {}
            this.http.getHcServerAddress(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.http.sqlUrl = res.data
                }
            })
        },
        // token
        isTokenVaildity () {
            let token = localStorage.getItem('spaceMenu')
            if (!token) {
                // this.$router.push({name: 'login'})
            }
            let oData = {
                token: token
            }
            this.http.isTokenVaildity(oData).then(res => {
                if (res.status === this.GLOBAL.success) {

                } else {
                    // this.$router.push({name: 'login'})
                }
            })
        },
        getStorageInfo () {
            let oData = {
                key: 'token'
            }
            this.http.getStorageInfo(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    localStorage.setItem('token', res.data)
                }
            })
        }
    },
    destroyed () {
        clearInterval(this.t1)
        Bus.$off('closeRight')
        Bus.$off('exit')
    }
}
</script>

<style lang="scss" scoped>
.box{
    display: -webkit-box;
    height: calc(100% - 40px);
    width: 100%;
}
.menu{
    top: 40px;
    width: 350PX;
    height: 100%;
//   border: 1px solid black;
}
.view{
  height: 100%;
  width: calc(100% - 350px);
  position: relative;
//   padding: 5px 20px 5px 0px;
//   background-color: #1D1F20;
}
</style>

<style lang="scss">
</style>



