<!--IDE-->
<template>
    <section id="menu1" @contextmenu.prevent="rightClick">
        <Tree ref='tree' :treeData='treeData' :treeCheckBox='treeCheckBox' :treeExpandAll='treeExpandAll' :treeKeysId='treeKeysId'
           @treeNodeClick='treeNodeClick' @treeRename='treeRename' @treeNewfile='treeNewfile' @treeDelete='treeDelete' @treeNewTask='treeNewTask' pagetab="1"></Tree>
           <rightMenu ref="rightMenu" :menuShow="rightMenuShow" @newFolder="newFolder" @newFile="newFile"></rightMenu>
    </section>
</template>

<script>
import Tree from '@/components/tree'
import rightMenu from '@/components/frame/rightMenu'
import {mapState} from 'vuex'
import Bus from '@/bus'
export default {
    name: 'menu1',
    components: {
        Tree,
        rightMenu
    },
    data () {
        return {
            treeData: [],
            treeCheckBox: false,
            treeExpandAll: false,
            treeKeysId: [],
            nodeData: {},
            historyMenu: -2,
            adjustMenu: -3,
            taskMenu: 3,
            rightMenuShow: false
        }
    },
    created () {
        this.getFolders()
        Bus.$on('closeRight', () => {
            if (this.selectPage === '1') {
                setTimeout(() => {
                    this.rightMenuShow = false
                    this.hiddenMenu()
                }, 0)
            }
        })
        Bus.$on('closeRightMenu', () => {
            this.rightMenuShow = false
        })
    },
    computed: {
        ...mapState(['selectPage', 'codeName', 'codeFolderId', 'codeData', 'codeFile', 'codeFileSave'])
    },
    watch: {

    },
    mounted () {
        // this.$refs.tree.loadSucceed(null)
    },
    methods: {
        // 
        getFolders () {
            let oData = {
                spaceId: 'debug'
            }
            this.http.getFolders(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    // console.log('')
                    this.treeData = res.data
                    // setTimeout(() => {
                    //     this.$refs.tree.loadSucceed(null)
                    // }, 0)
                }
            }).catch(err => {
                return err
                // console.log(err)
                // this.$message({type: 'error', message: err})
            })
        },
        treeNodeClick (data) {
            this.nodeData = data
        },
        // 
        treeRename (data, rename) {
            let oData = {
                spaceId: 'debug',
                folderId: data.folderId,
                newName: rename
            }
            let oldFolderId = data.folderId
            let oldFolderIdArr = oldFolderId.split('/')
            let newFolderId = ''
            for (let i = 0; i < oldFolderIdArr.length - 1; i++) {
                newFolderId += oldFolderIdArr[i] + '/'
            }
            newFolderId += rename
            let idx = this.codeFolderId.indexOf(data.folderId)
            this.http.renameFile(oData).then(res => {
                if (res.status === 200) {
                    if (idx !== -1) {
                        // this.codeName.splice(idx, 1, rename)
                        // this.codeData.splice(idx, 1, res)
                        // this.codeFile[res.folderId] = this.codeFile[data.folderId]
                        // delete this.codeFile[data.folderId]
                        // this.codeFileSave[res.folderId] = this.codeFileSave[data.folderId]
                        // delete this.codeFileSave[data.folderId]
                        // delete this.codeFile[data.folderId]
                        this.$store.commit('codeNameSplice', idx)
                        this.$store.commit('codeDataSplice', idx)
                        let temp = {
                            newFoloderId: res.data.folderId,
                            oldFolderId: data.folderId
                        }
                        this.$store.commit('codeFileSplice', temp)
                    }
                    for (let key in data) {
                        data[key] = res.data[key]
                    }
                    this.$refs.tree.renameSuccess(data)
                    let folderId = {
                        new: newFolderId,
                        old: oldFolderId
                    }
                    this.$store.commit('fileRename', folderId)
                }
            }).catch(err => {
                return err
            })
        },
        treeNewfile (node, fileName, data) {
            // 
            // console.log('')
            let oData = {
                spaceId: 'debug',
                parentId: node.parent.data.folderId,
                folderName: fileName,
                isFile: data.isFile,
                fileContent: ''
            }
            this.http.createFile(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.$refs.tree.newfileSuccess(res.data)
                }
            }).catch(err => {
                return err
            })
        },
        treeDelete (data) {
            // 
            let oData = {
                spaceId: 'debug',
                folderId: data.folderId
            }
            this.http.deleteFile(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.$refs.tree.dltSuccess()
                    this.$message({type: 'success', message: ''})
                }
            }).catch(err => {
                return err
                // this.$message({type: 'error', message: err})
            })
        },
        treeNewTask () {

        },
        hiddenMenu () {
            if (this.$refs.tree) {
                this.$refs.tree.hiddenMenu()
            }
        },
        // 
        rightClick (e) {
            if (this.$refs.tree) {
                this.$refs.tree.hiddenMenu()
            }
            this.rightMenuShow = true
            this.$refs.rightMenu.setPosi(e.clientX, e.clientY)
        },
        // 
        newFolder () {
            this.$refs.tree.newFolderBottom()
        },
        // 
        newFile () {
            this.$refs.tree.newFileBottom()
        }
    },
    destroyed () {
        Bus.$off('closeRight')
        Bus.$off('closeRightMenu')
    }
}
</script>

<style scoped>
#menu1{
    height: 100%;
    width: 100%;
    background-color: #252525;
    color: #fff;
    overflow-x: hidden;
    overflow-y: scroll;
}
.scrollbar{
    width: 30px;
    height: 300px;
    margin: 0 auto;

}
</style>

<style lang="scss" scope>
#menu1{
    .el-tree{
        background-color:transparent;
        color: #fff;
    }
    .el-tree-node:focus>.el-tree-node__content{
        background-color: #0E639C;
    }
    .el-tree-node__content:hover{
        background-color: #0E639C;
    }
}
</style>



