<!---->
<template>
    <section class="log">
        <div class="title">
            
            <img :src="clearImg" @mouseover="clearImg=clearImg2" @mouseleave="clearImg=clearImg1" @click="emptyLogs" title="">
        </div>
        <div class="content">
            <div style="padding: 5px;" v-for="(item, index) in logs" :key="index">
                {{formData(item.createTime)}}   {{item.message}}
            </div>
        </div>
        <div class="height" @mousedown="setDiffer" @mouseup="changeHeightClicked=false"></div>
    </section>
</template>

<script>
import unit from '@/assets/unit/timeConversion'
import {mapState} from 'vuex'
export default {
    name: 'log',
    data () {
        return {
            windowHeight: 0,
            changeHeightClicked: false,
            differ: 0,
            logHeight: 0,
            clearImg: require('@/assets/img/clear.png'),
            clearImg1: require('@/assets/img/clear.png'),
            clearImg2: require('@/assets/img/clearClick.png')
        }
    },
    computed: {
        ...mapState(['selectPage', 'logs'])
    },
    created () {

    },
    mounted () {
        this.windowHeight = document.documentElement.clientHeight
        this.logHeight = document.getElementsByClassName('log')[parseInt(this.selectPage) - 1].offsetHeight
    },
    methods: {
        setDiffer () {
            this.changeHeightClicked = true
            this.differ = (this.logHeight - (this.windowHeight - window.event.clientY))
        },
        changeHeight (clientY) {
            // console.log(clientY)
            let newHeightNum = this.windowHeight - window.event.clientY + this.differ
            if (newHeightNum < -200) {

            } else if (newHeightNum > document.body.offsetHeight - 500) {

            } else {
                let newHeight = (newHeightNum + 300) + 'px'
                document.getElementById('code' + this.selectPage).style.height = 'calc(100% - ' + newHeight + ')'
                document.getElementsByClassName('log')[parseInt(this.selectPage) - 1].style.height = newHeight
                this.logHeight = newHeightNum
            }
        },
        formData (date) {
            let a = unit.formatDate(date)
            return a
        },
        emptyLogs () {
            this.$store.commit('emptyLogs')
        }
    }
}
</script>

<style lang="scss" scoped>
::-webkit-scrollbar {
/**/
    // display: none;
}
.log{
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    background-color: #1D1F20;
    border:1px solid rgba(77,77,77,1);
    position: relative;
    .title{
        background-color: #252525;
        border:1px solid rgba(77,77,77,1);
        height: 36px;
        line-height: 36px;
        padding-left: 5px;
        img{
            float: right;
            margin-right: 20px;
            margin-top: 7px;
            position:relative;
            z-index: 10;
        }
    }
    .content{
        height: calc(100% - 36px);
        overflow: scroll;
        // overflow-x: hidden;
    }
    .height{
        height: 30px;
        // border: 1px solid red;
        position: absolute;
        width: 100%;
        cursor: n-resize;
        top: 0;
    }
}
</style>



