<!--IDE-->
<template>
    <section id="menu1">
        <div class="add" @click="newAppLet">+ </div>
        <Tree ref='tree' class="tree" :treeData='treeData' :treeCheckBox='treeCheckBox' :treeExpandAll='treeExpandAll' :treeKeysId='treeKeysId'
           @treeNodeClick='treeNodeClick' @treeRename='treeRename' @treeNewfile='treeNewfile' @treeDelete='treeDelete' @treeNewTask='treeNewTask' pagetab="2"></Tree>
        <el-dialog
         :visible.sync="AppLetShow"
         width="860px"
         custom-class="createAppLet"
         :before-close="handleClose">
            <div slot="title" style="display:flex;">
                <img style="height:20px;width:26px;" :src="require('@/assets/img/logo.png')">
                <div style="line-height:20px;margin-left:10px;"></div>
            </div>
            <createAppLet @createApplet="getMiniList1" @cacel="AppLetShow=false" :dialogShow="AppLetShow"></createAppLet>
         </el-dialog>
    </section>
</template>

<script>
import Tree from '@/components/tree2'
import createAppLet from '@/components/createAppLet'
import {mapState} from 'vuex'
import Bus from '@/bus'
export default {
    name: 'menu1',
    components: {
        Tree,
        createAppLet
    },
    data () {
        return {
            treeData: [],
            treeCheckBox: false,
            treeExpandAll: false,
            treeKeysId: [],
            nodeData: {},
            historyMenu: -2,
            adjustMenu: -3,
            taskMenu: 3,
            AppLetShow: false
        }
    },
    created () {
        // this.getFolders()
        this.getMiniList()
        Bus.$on('closeRight', () => {
            if (this.selectPage === '2') {
                setTimeout(() => {
                    this.hiddenMenu()
                }, 0)
            }
        })
    },
    computed: {
        ...mapState(['selectPage', 'appLet'])
    },
    watch: {

    },
    mounted () {
        // this.$refs.tree.loadSucceed(null)
    },
    methods: {
        // 
        getFolders (id) {
            let oData = {
                spaceId: id
            }
            this.http.getFolders(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    // console.log('')
                    this.treeData = res.data
                    // setTimeout(() => {
                    //     this.$refs.tree.loadSucceed(null)
                    // }, 0)
                }
            }).catch(err => {
                return err
            })
        },
        getMiniList () {
            this.AppLetShow = false
            let oData = {}
            this.http.getMiniList(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.treeData = res.data
                }
            }).catch(err => {
                return err
                // this.$message({type: 'error', message: err})
            })
        },
        getMiniList1 () {
            this.AppLetShow = false
            let oData = {}
            this.http.getMiniList(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    // this.treeData = res.data
                    // console.log(res.data)
                    // debugger
                    for (let i = 0; i < res.data[0].children.length; i++) {
                        let bfind = false
                        for (let j = 0; j < this.treeData[0].children.length; j++) {
                        //     bfind = false
                            // console.log(j)
                            if (this.treeData[0].children[j].folderId === res.data[0].children[i].folderId) {
                                bfind = true
                                break
                            }
                        }
                        if (!bfind) {
                            // console.log(i)
                            this.treeData[0].children.push(res.data[0].children[i])
                        }
                    }
                }
            }).catch(err => {
                return err
                // this.$message({type: 'error', message: err})
            })
        },
        treeNodeClick (data) {
            this.nodeData = data
        },
        treeRename (data, rename) {
            let oData = {
                spaceId: data.spaceId,
                folderId: data.folderId,
                newName: rename
            }
            // console.log(this.appLet)
            let idx = this.appLet.codeFolderId.indexOf(data.folderId)
            this.http.renameFile(oData).then(res => {
                if (res.status === 200) {
                    if (idx !== -1) {
                        // this.codeName.splice(idx, 1, rename)
                        // this.codeData.splice(idx, 1, res)
                        // this.codeFile[res.folderId] = this.codeFile[data.folderId]
                        // delete this.codeFile[data.folderId]
                        // this.codeFileSave[res.folderId] = this.codeFileSave[data.folderId]
                        // delete this.codeFileSave[data.folderId]
                        // delete this.codeFile[data.folderId]
                        this.$store.commit('appCodeNameSplice', idx)
                        this.$store.commit('appCcodeDataSplice', idx)
                        // let temp = {
                        //     newFoloderId: res.data.folderId,
                        //     oldFolderId: data.folderId
                        // }
                        // this.$store.commit('appCodeFileSplice', temp)
                    }
                    for (let key in data) {
                        data[key] = res.data[key]
                    }
                    this.$refs.tree.renameSuccess(data)
                }
            }).catch(err => {
                return err
            })
        },
        treeNewfile (node, fileName, data) {
            // 
            // console.log('')
            // console.log(data, name)
            let oData = {
                spaceId: node.parent.data.spaceId,
                parentId: node.parent.data.folderId,
                folderName: fileName,
                isFile: data.isFile,
                fileContent: ''
            }
            this.http.createFile(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.$refs.tree.newfileSuccess(res.data)
                }
            }).catch(err => {
                return err
            })
        },
        treeDelete (data) {
            // console.log(data)
            if (data.type !== '' && data.type !== '') {
                // 
                let oData = {
                    spaceId: data.spaceId,
                    folderId: data.folderId
                }
                this.http.deleteFile(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$refs.tree.dltSuccess()
                        this.$message({type: 'success', message: ''})
                    }
                }).catch(err => {
                    return err
                // this.$message({type: 'error', message: err})
                })
            } else {
                // 
                let oData = {
                    space_id: data.spaceId
                }
                this.http.miniDelete(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$refs.tree.dltSuccess()
                        this.$message({type: 'success', message: ''})
                    }
                }).catch(err => {
                    return err
                })
            }
        },
        treeNewTask (data) {
            return false
        },
        hiddenMenu () {
            if (this.$refs.tree) {
                this.$refs.tree.hiddenMenu()
            }
        },
        newAppLet () {
            this.AppLetShow = true
        },
        handleClose () {
            this.AppLetShow = false
        },
        // 
        createAppLet1 () {

        }
    },
    destroyed () {
        Bus.$off('closeRight')
    }
}
</script>

<style lang="scss" scoped>
#menu1{
    height: 100%;
    width: 100%;
    background-color: #252525;
    color: #fff;
    overflow-x: hidden;
    overflow-y: scroll;
    .add{
        width: 90%;
        height: 30px;
        margin: auto;
        margin-top: 20px;
        margin-bottom: 20px;
        line-height: 30px;
        background-color: #0e639c;
        cursor: pointer;
    }
    .tree {
        height: calc(100% - 70px);
    }
}
.scrollbar{
    width: 30px;
    height: 300px;
    margin: 0 auto;

}
</style>

<style lang="scss" scope>
#menu1{
    .el-tree{
        background-color:transparent;
        color: #fff;
    }
    .el-tree-node:focus>.el-tree-node__content{
        background-color: #0E639C;
    }
    .el-tree-node__content:hover{
        background-color: #0E639C;
    }
}
.createAppLet{
    background:rgba(37,37,37,1) !important;
    box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 0.5) !important;
    border-radius:4px !important;
    .el-dialog__header{
        text-align: left;
        .el-dialog__title{
            color: #CCCCCC;
        }
    }
}
</style>



