<!---->
<template>
    <section class="appLetSet">
       <el-form ref="form" :model="formData" label-width="120px" label-position="right" :rules="rules1">
            <el-form-item label="" prop="isFullScreen">
                <div class="formItemContent">
                    <el-radio-group v-model="formData.isFullScreen">
                        <el-radio :label="'0'"></el-radio>
                        <el-radio :label="'1'"></el-radio>
                    </el-radio-group>
                </div>
            </el-form-item>
            <el-form-item label="" prop="indexUrl">
                <el-input v-model="formData.indexUrl" @focus="inputFocus" @blur="inputBlur" :maxlength="64"></el-input>
            </el-form-item>
            <el-form-item label="" prop="appLetName">
                <el-input v-model="formData.appLetName" @focus="inputFocus" @blur="inputBlur" :maxlength="30"></el-input>
            </el-form-item>
            <el-form-item label="">
                <div class="formItemContent">
                    <el-upload
                        class="avatar-uploader"
                        :action="uploadUrl"
                        :show-file-list="false"
                        :http-request="uploadLogo">
                        <img v-if="logoUrl" :src="logoUrl" class="avatar" style="float:left;height:100px;width:100px;">
                        <img v-else :src="img1" style="height: 100px;width: 100px;">
                    </el-upload>
                    <div style="position:absolute;top:0;left:110px;font-size:12px;opacity:0.6;">
                        <span></span>
                        <br>
                        <span>100*100PNG300K</span>
                    </div>
                </div>
            </el-form-item>
            <div style="padding: 20px 0;text-align:center;">
                <el-button slot="trigger" size="medium" type="primary" style="color:white;" @click="submitForm"></el-button>
                <el-button slot="trigger" size="medium" type="info" plain style="color:black;" @click="$emit('cacel')"></el-button>
            </div>
       </el-form>
    </section>
</template>

<script>
import Bus from '@/bus'
export default {
    props: ['appSetData'],
    name: 'appLetSet',
    data () {
        return {
            formData: {
                appLetType: '',
                isFullScreen: '0',
                indexUrl: '',
                appLetName: '',
                logoUrl: '',
                offLine: '',
                createAppLetId: ''
            },
            rules1: {
                indexUrl: [{required: true, message: ''}],
                appLetName: [{required: true, message: ''}]
            },
            appData: {},
            logoUrl: '',
            logoFile: {},
            uploadUrl: '',
            offlineShow: false,
            img1: require('@/assets/img/applet.png')
        }
    },
    created () {

    },
    watch: {
        appSetData: {
            immediate: true,
            handler: function (newValue, oldValue) {
                // console.log(newValue)
                this.formData.logoUrl = ''
                this.logoUrl = ''
                this.formData.indexUrl = newValue.indexPage
                this.formData.appLetName = newValue.name
                this.formData.isFullScreen = newValue.isFullScreen
                this.img1 = newValue.iconUserUpload
                this.appData = newValue
            }
        }
    },
    methods: {
        uploadLogo (data) {
            this.logoUrl = URL.createObjectURL(data.file)
            this.formData.logoUrl = URL.createObjectURL(data.file)
            this.logoFile = data.file
            return true
        },
        // 
        uploadLogo1 () {
            if (this.formData.logoUrl !== '') {
                let headers = {
                    'space_id': this.appData.spaceId
                }
                let oData = new FormData()
                oData.append('icon', this.logoFile)
                // console.log(this.logoFile)
                // console.log(oData)
                this.http.miniUploadIcon(headers, oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.miniRename()
                    }
                })
            } else {
                this.miniRename()
            }
        },
        // 
        submitForm () {
            this.$refs.form.validate((valid) => {
                // console.log(valid)
                if (valid) {
                    this.uploadLogo1()
                } else {
                    // console.log('error submit!!');
                    return false
                }
            })
        },
        miniRename () {
            // if (this.formData.appLetName !== this.appData.name) {
            let oData = {
                space_id: this.appData.spaceId,
                newName: this.formData.appLetName,
                isFullScreen: this.formData.isFullScreen
            }
            this.http.miniRename(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    Bus.$emit('appletAttr', res.data)
                    this.miniEditIndexPage()
                }
            }).catch(err => {
                return err
            })
            // } else {
            // this.miniEditIndexPage()
            // this.$emit('cacel')
            // }
        },
        miniEditIndexPage () {
            if (this.formData.indexUrl !== this.appData.indexPage) {
                let oData = {
                    space_id: this.appData.spaceId,
                    newIndexPage: this.formData.indexUrl
                }
                this.http.miniEditIndexPage(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$emit('cacel')
                    }
                }).catch(err => {
                    return err
                })
            } else {
                this.$emit('cacel')
            }
        },
        upData (data) {
            // console.log(data)
            this.formData.indexUrl = data.indexPage
            this.formData.appLetName = data.name
            if (data.iconUserUpload) {
                this.img1 = data.iconUserUpload
            } else {
                this.img1 = data.icon
            }
            this.appData = data
        },
        inputFocus (e) {
            e.target.style.border = '1px solid #007BFF'
        },
        inputBlur (e) {
            e.target.style.border = '1px solid #3c3c3c'
        }
    }
}
</script>

<style lang="scss" scoped>
.appLetSet{
    // border: 1px solid red;
    padding: 0 12%;
    .formItemContent{
        text-align: left;
    }
}
</style>

<style lang="scss">
.appLetSet{
    .el-form-item__label{
        color: #CCCCCC  !important;
    }
    .el-input__inner{
        background-color: #3c3c3c;
        color: #cccccc;
        border: 1px solid #3c3c3c;
    }
      .avatar-uploader .el-upload {
    // border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    line-height: 0;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>



