<!---->
<template>
    <section id="createAppLet">
        <el-form ref="form" :model="formData" label-width="120px" label-position="right" :rules="rules1" v-if="dialogShow">
            <el-form-item label="" prop="appLetType">
                <div class="formItemContent">
                    <el-radio-group v-model="formData.appLetType">
                        <el-radio :label="''"></el-radio>
                        <el-radio :label="''"></el-radio>
                        <!-- <el-radio :label="''"></el-radio> -->
                    </el-radio-group>
                </div>
            </el-form-item>
            <div v-show="formData.appLetType !== ''">
                <el-form-item label="">
                    <div class="formItemContent">
                        <el-radio-group v-model="formData.isFullScreen">
                            <el-radio :label="0"></el-radio>
                            <el-radio :label="1"></el-radio>
                        </el-radio-group>
                    </div>
                </el-form-item>
                <el-form-item label="" prop="indexUrl">
                    <el-input v-model="formData.indexUrl" @focus="inputFocus" @blur="inputBlur" :maxlength="64"></el-input>
                </el-form-item>
                <el-form-item label="" prop="appLetName">
                    <el-input v-model="formData.appLetName" @focus="inputFocus" @blur="inputBlur" :maxlength="30"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <div class="formItemContent">
                        <el-upload
                            class="avatar-uploader"
                            :action="uploadUrl"
                            :show-file-list="false"
                            :http-request="uploadLogo">
                            <img v-if="logoUrl" :src="logoUrl" class="avatar" style="float:left;height:100px;width:100px;">
                            <img v-else :src="require('@/assets/img/applet.png')" style="height: 100px;width: 100px;">
                        </el-upload>
                        <div style="position:absolute;top:0;left:110px;font-size:12px;opacity:0.6;">
                            <span></span>
                            <br>
                            <span>100*100PNG300K</span>
                        </div>
                    </div>
                </el-form-item>
                <el-form-item label="" v-show="formData.appLetType === ''" prop="offLine">
                    <div class="formItemContent">
                        <el-upload
                            class="avatar-uploader-ios"
                            ref="offLine"
                            :action="uploadUrl"
                            :show-file-list="true"
                            :on-remove="removeOffLine"
                            :http-request="uploadOffLine"
                            :before-upload="beforeOffLine">
                            <el-button slot="trigger" size="small" type="primary" style="color:white;"></el-button>
                        </el-upload>
                        <div style="position:absolute;top: 0;left:25%;font-size:12px;opacity:0.6;">
                            <span>.zip,1</span>
                        </div>
                    </div>
                </el-form-item>
            </div>
            <!-- <div v-if="formData.appLetType === ''">
                <el-form-item label="" prop="appLetId">
                    <el-select v-model="formData.appLetId" filterable placeholder="" style="width:100%;" @change="setAppId">
                        <el-option v-for="(item, index) in appIdList" :key="index" :value="item.name + '|' + item.uniqueCode" style="height:100%;" class="appIdOption">
                            <div class="selectBox">
                                <el-row>
                                    <el-col :span="2"><img :src="item.logoUrl"></el-col>
                                    <el-col :span="12" :title="item.name"><span>{{item.name}}</span></el-col>
                                    <el-col :span="10" :title="item.uniqueCode"><span>{{item.uniqueCode}}</span></el-col>
                                </el-row>
                            </div>
                        </el-option>
                    </el-select>
                </el-form-item>
            </div> -->
            <div style="padding: 20px 0;">
                <el-button slot="trigger" size="small" type="primary" style="color:white;" @click="submitForm"></el-button>
                <el-button slot="trigger" size="small" type="info" plain style="color:black;" @click="$emit('cacel')"></el-button>
            </div>
        </el-form>
    </section>
</template>

<script>
export default {
    props: ['dialogShow'],
    name: 'createAppLet',
    data () {
        var offLine = (rule, value, callback) => {
            if (this.formData.appLetType === '') {
                if (this.formData.offLine === '') {
                    callback(new Error(''))
                } else {
                    callback()
                }
            } else {
                callback()
            }
        }
        return {
            a: '',
            formData: {
                appLetType: '',
                isFullScreen: 0,
                indexUrl: '',
                appLetName: '',
                logoUrl: '',
                offLine: '',
                createAppLetId: '',
                appLetId: ''
            },
            rules1: {
                indexUrl: [{required: true, message: ''}],
                appLetName: [{required: true, message: ''}],
                offLine: [{required: true, validator: offLine, message: ''}],
                appLetId: [{required: true, message: 'ID'}]
            },
            logoUrl: '',
            logoFile: {},
            uploadUrl: '',
            offlineShow: false,
            appIdList: [],
            appId: '',
            // 
            submited: true
        }
    },
    watch: {
        dialogShow (newValue, oldValue) {
            if (newValue === false) {
                this.formData = {
                    appLetType: '',
                    indexUrl: '',
                    appLetName: '',
                    logoUrl: '',
                    offLine: {},
                    createAppLetId: ''
                }
                this.logoFile = {}
                this.logoUrl = ''
                if (this.$refs.offLine) {
                    this.$refs.offLine.clearFiles()
                }
                this.getAppletList()
            }
        }
    },
    created () {
        this.getAppletList()
    },
    methods: {
        uploadIconSuccess () {

        },
        beforeOffLine (file) {
            if (file.size > 1024 * 1024 * 10) {
                this.$message({type: 'warning', message: '10M'})
                return false
            }
            let arr = file.name.split('.')
            if (arr[arr.length - 1] === 'zip') {
                return true
            } else {
                this.$message({type: 'warning', message: 'zip'})
                return false
            }
        },
        uploadOffLine (data) {
            this.formData.offLine = data.file
        },
        // 
        uploadOffLine1 () {
            if (this.formData.appLetType === '') {
                let headers = {
                    'space_id': encodeURI(this.createAppLetId)
                }
                let oData = new FormData()
                // this.$message('')
                oData.append('offLine', this.formData.offLine)
                this.http.miniUploadOfflineZip(headers, oData).then(res => {
                    // console.log(res)
                    if (res.status === this.GLOBAL.success) {
                        // this.$message('')
                        this.$emit('createApplet')
                        this.submited = true
                    }
                }).catch(err => {
                    // console.log(err)
                    return err
                })
            } else {
                // let temp = {

                // }
                this.$emit('createApplet')
                this.submited = true
            }
        },
        removeOffLine () {

        },
        uploadLogo (data) {
            this.logoUrl = URL.createObjectURL(data.file)
            this.formData.logoUrl = URL.createObjectURL(data.file)
            this.logoFile = data.file
            return true
        },
        // 
        uploadLogo1 () {
            if (this.formData.logoUrl !== '') {
                let headers = {
                    'space_id': encodeURI(this.createAppLetId)
                }
                let oData = new FormData()
                oData.append('icon', this.logoFile)
                this.http.miniUploadIcon(headers, oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.uploadOffLine1()
                    }
                }).catch(err => { return err })
            } else {
                this.uploadOffLine1()
            }
        },
        miniUploadOfflineZip () {
            if (this.formData.appLetType === '') {

            }
        },
        // 
        submitForm () {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if (this.formData.appLetType !== '') {
                        this.miniCreate()
                    } else {
                        this.miniCreateById()
                    }
                } else {
                    // console.log('error submit!!');
                    return false
                }
            })
        },
        setAppId (data) {
            let arr = data.split('|')
            this.formData.appLetId = arr[0]
            this.appId = arr[1]
        },
        // ID
        miniCreateById () {
            // let appId
            let oData = {
                id: this.appId,
                ip: this.http.sqlUrl,
                token: localStorage.getItem('token')
            }
            this.http.miniCreateById(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.$message({type: 'success', message: ''})
                    this.$emit('createApplet')
                    this.submited = true
                }
            })
        },
        // id
        getAppletList () {
            let oData = {
                // nameOrCode: this.formData.appLetId
                nameOrCode: ''
            }
            this.http.getAppletList(oData).then(res => {
                // console.log(res)
                if (res.data.code === 100) {
                    this.appIdList = res.data.result
                } else {
                    // this.$message({type: 'error', message: res.data.resultDes})
                    localStorage.removeItem('token')
                    // this.$router.push({name: 'login'})
                }
            })
        },
        // 
        miniCreate () {
            if (this.submited === false) {
                return false
            }
            this.submited = false
            let oData = {
                type: this.formData.appLetType,
                name: this.formData.appLetName,
                indexPage: this.formData.indexUrl,
                isFullScreen: this.formData.isFullScreen
            }
            this.http.miniCreate(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    // this.$emit('createApplet')
                    this.createAppLetId = res.data
                    this.uploadLogo1()
                }
            }).catch(err => {
                return err
            })
        },
        inputFocus (e) {
            e.target.style.border = '1px solid #007BFF'
        },
        inputBlur (e) {
            e.target.style.border = '1px solid #3c3c3c'
        }
    }
}
</script>

<style lang="scss" scoped>
*{
    color: #CCCCCC;
}
#createAppLet{
    // border: 1px solid red;
    padding: 0 12%;
    .formItemContent{
        text-align: left;
    }
}
</style>

<style lang="scss">
#createAppLet{
    .el-form-item__label{
        color: #CCCCCC  !important;
    }
    .el-input__inner{
        background-color: #3c3c3c !important;
        color: #cccccc;
        border: 1px solid #3c3c3c !important;
    }
      .avatar-uploader .el-upload {
    // border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    line-height: 0;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
.selectBox{
    img{
        height: 34px;
        box-sizing: border-box;
    }
    .el-row{
        height:50px;
        padding:8px 0 8px 0;
        .el-col-2{
            height:34px;
        }
        .el-col-12{
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: keep-all;
        }
        .el-col-10{
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: keep-all;
        }
    }
}
.el-select-dropdown__list{
    padding: 0 0 6px 0;
    // background-color: #606266;
    .hover{
        background-color: #0E639C !important;
    }
}
.el-select-dropdown{
    background-color: #3C3C3C !important;
    border: 1px solid #3C3C3C !important;
}
.popper__arrow{
    border-bottom-color: #3C3C3C !important;
}
.popper__arrow:after{
    border-bottom-color: #3C3C3C !important;
    display: none !important;
}
</style>



