<!---->
<template>
    <section class="rightMenu1">
        <ul class="menu" v-if="menuShow">
            <li @click="newFolder">
                <div></div>
            </li>
            <li @click="newFile">
                <div></div>
            </li>
        </ul>
    </section>
</template>

<script>
export default {
    name: 'rightMenu',
    props: ['menuShow'],
    data () {
        return {

        }
    },
    methods: {
        setPosi (x, y) {
            let element = document.getElementsByClassName('rightMenu1')[0]
            element.style.top = y + 'px'
            element.style.left = x + 'px'
        },
        // 
        rightNewFolder () {

        },
        newFolder () {
            this.$emit('newFolder')
        },
        newFile () {
            this.$emit('newFile')
        }
    }
}

</script>
<style lang="scss" scoped>
.rightMenu1{
    position: absolute;
    top: 0;
}
.menu {
    display: inline-block;
    position: fixed;
    box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 1);
    padding: 0px;
    list-style-type: none;
    background-color: #252525;
    z-index: 99;
    li {
        // padding:5px 60px 5px 15px;
        padding: 0 60px 0 15px;
        line-height: 30px;
        color: #fff;
        text-align:left;
    }
    li:hover {
        // color: blue;
        background-color: #0E639C;
    }
}
</style>



