<template>
    <section id="share" @click='hiddenMenu'  @contextmenu='hiddenMenu'>
        <div class="path">
            <img :src="require('@/assets/img/folder.png')" alt="">
            <div v-for="(path,index) in paths" :key="index" style="display:inline-block">
                <span style="cursor:default" @click='linkPath(path)'>{{(index != 0)?">":''}}{{ path.name }}</span>
            </div>
        </div>
        <div class="folderCont">
            <div class="folderFunction">
                <div class="folderOperate">
                    <el-button @click="onNewFolder"><i class="icon-new"></i> </el-button>
                    <!-- <el-button><i class="icon-file"></i></el-button> -->
                    <el-upload
                        class="upload-demo upload-file"
                        :action= "uploadFileUrl"
                        :http-request="uploadFileRequest"
                        :show-file-list="false"
                        :file-list="fileList"
                        :before-upload="beforeUpload"
                        :on-success="uploadIconSuccess"
                        :on-error="uploadIconError"
                        >
                        <el-button><i class="icon-file"></i></el-button>
                    </el-upload>
                    <el-button style="display: none"><i class="icon-folder"></i></el-button>
                </div>
                <div class="folderSearch">
                    <el-input
                        v-model = "iptSearch"
                        placeholder=""
                        @keyup.enter.native="onSearch">
                        <el-button slot="suffix" icon="el-icon-search" @click="onSearch"></el-button>
                    </el-input>
                    <el-tooltip class="itemTip" effect="light" content="" placement="bottom" style="display: none">
                        <span slot="content" v-show="shareState"></span>
                        <span slot="content" v-show="!shareState"></span>
                        <i :class="shareState ? 'icon-list' : 'icon-shrink'" @click="onIconShrink"></i>
                    </el-tooltip>
                </div>
            </div>
            <div class="folder showWay" :class="shareState ? 'shrink' : 'list'"  @contextmenu.stop.prevent>
                <ul class="itemBox" v-if="shareState">
                    <li class="item" :class="{ active:item.isActive }" ref="item"
                        v-for="(item,index) in listData" :key="index"
                        @click="onChoosed(index,item)"
                        @dblclick="onDblClickRow(item,null)"
                        @contextmenu.stop.prevent="onRClick(index)">
                        <!-- <div class="icon-item">
                            <img :src="item.icon" alt="">
                        </div>
                        <p class="name-item">
                            <el-input @blur.prevent="onBlur(index)"
                                @keyup.enter.native="$event.target.blur"
                                :class="{border_input:item.focus}"
                                :ref="'input'+index" v-model="item.name"
                                :disabled='!item.isDisabled' :focus="item.focus"></el-input>
                        </p>
                        <ul class="item-mask" v-if="item.isFile" :id="'mask' + index">
                            <li class="mask-list">
                                <span class="ma
                                sk-title"></span>
                                <span class="mask-cont">{{item.name.substring(item.name.lastIndexOf('.') + 1)}}</span>
                            </li>
                            <li class="mask-list">
                                <span class="mask-title"></span>
                                <span class="mask-cont">{{item.size}}</span>
                            </li>
                            <li class="mask-list">
                                <span class="mask-title"></span>
                                <span class="mask-cont">{{item.createTimeTrans}}</span>
                            </li>
                        </ul>
                        <ul class="item-mask" v-else>
                            <li class="mask-list">
                                <span class="mask-title"></span>
                                <span class="mask-cont">{{item.createTimeTrans}}</span>
                            </li>
                            <li class="mask-list">
                                <span class="mask-title"></span>
                                <span class="mask-cont">{{item.size}}</span>
                            </li>
                        </ul> -->
                        <!--  -->
                        <!-- <ul class="menu" v-show="showMenu == index" :id="'shareRM' + index">
                            <li v-for="(itemM,i) in menuList" :key="i" @click="onLiClick(i,index,item)">
                                <a v-show="i == 2" :href="'http://172.19.1.200:5000/downloadFileOrZip?spaceId=shareSpace&folderId='+item.folderId"  download="item.name">
                                    {{itemM.name}}
                                </a>
                                <div v-show="!(i == 2)">{{itemM.name}}</div>
                            </li>
                        </ul> -->
                    </li>
                </ul>
                <el-table class="listTable" v-if="!shareState"
                    :data="listData"
                    style="width: 100%"
                    empty-text=" "
                    :row-class-name="tableRowClassName"
                    @row-click="onChoosed2"
                    @row-contextmenu="onRClickRow"
                    @row-dblclick="onDblClickRow"
                    height="100%">
                    <el-table-column
                    prop="name"
                    label="">
                        <template slot-scope="scope">
                            <div class="tableMask" :ref="'item' + scope.row.index">
                                <div class="listImg">
                                    <img :src="scope.row.icon" alt="">
                                </div>
                                <div class="listName">
                                    <el-input :id="'input' + scope.row.index"
                                        @blur.prevent="onBlur(scope.$index)"
                                        @keyup.enter.native="$event.target.blur"
                                        :class="{border_input:scope.row.focus}"
                                        :ref="'input' + scope.row.index"
                                        v-model="scope.row.name"
                                        :disabled='!scope.row.isDisabled'
                                        :focus="scope.row.focus">
                                    </el-input>
                                </div>
                                <!-- <ul class="item-mask" trigger="hover" style="position: fixed" v-show="scope.row.isFile">
                                    <li class="mask-list">
                                        <span class="mask-title"></span>
                                        <span class="mask-cont">2019.12.03</span>
                                    </li>
                                    <li class="mask-list">
                                        <span class="mask-title"></span>
                                        <span class="mask-cont">{{scope.row.size}}</span>
                                    </li>
                                    <li class="mask-list">
                                        <span class="mask-title"></span>
                                        <span class="mask-cont">{{scope.row.createTimeTrans}}</span>
                                    </li>
                                </ul>
                                <ul class="item-mask" trigger="hover" style="position: fixed" v-show="!scope.row.isFile">
                                    <li class="mask-list">
                                        <span class="mask-title"></span>
                                        <span class="mask-cont">{{scope.row.createTimeTrans}}</span>
                                    </li>
                                    <li class="mask-list">
                                        <span class="mask-title"></span>
                                        <span class="mask-cont">{{scope.row.size}}</span>
                                    </li>
                                </ul> -->
                                <!--  -->
                                <ul class="menu" v-show="showMenu == scope.row.index" :id="'shareRM' + scope.row.index">
                                    <li v-for="(item,i) in menuList" :key="i" @click="onLiClick(i,scope.row.index,scope.row)">
                                        <!-- <a v-show="i == 2" :href="'${this.http.shareUrl}/downloadFileOrZip?spaceId=shareSpace&folderId=${folderid}='+scope.row.folderId" download="scope.row.name">
                                          {{item.name}}
                                        </a>
                                        <div v-show="!(i == 2)">{{item.name}}</div> -->
                                        <div>{{item.name}}</div>
                                    </li>
                                 </ul>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="size"
                    label=""
                    width="200"
                    align="center">
                    </el-table-column>
                    <el-table-column
                    prop="createTimeTrans"
                    label=""
                    width="300"
                    align="center">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-dialog class="dlt-dialog" title="" :visible.sync="dialogDelete" width="400px" custom-class="dialogStyle">
            <span>&nbsp;{{deleteName}}&nbsp;</span>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="onConfirmDel"> </el-button>
                <el-button @click="dialogDelete = false"> </el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import Validators from '../../publicmethod/timeConversion.js'
import {mapState} from 'vuex'
import Bus from '@/bus'
export default {
    name: 'shareSpace',
    data () {
        return {
            isShrink: true, // 
            isDisabled: true, // 
            iptSearch: '', // 
            listData: [],
            allList: [], // 
            menuList: [{
                name: ''
            }, {
                name: ''
            }, {
                name: ''
            }], // 
            showMenu: null, // 
            isIptChange: false,
            // uploadFileUrl: 'http://172.19.1.200:5000/uploadFile', // 
            uploadFileUrl: '',
            fileList: [],
            paths: [], // 
            opFolderId: '',
            cacheData: {},
            sortAscending: true,
            dialogDelete: false,
            deleteName: '',
            deleteIndex: -1,
            newFolderCount: 0,
            newFolderName: '',
            newFolderList: [],
            newFolderBoolean: false,
            reNameVal: null,
            newFolderListData: [],
            file: '', // 
            parId: '',
            parData: null,
            isEmpty: true
        }
    },
    computed: {
        ...mapState(['shareState'])
    },
    props: ['isActive'],
    created () {
        // 
        this.paths = [
            {
                name: '../',
                folderId: '/'
            }
        ]
        this.opFolderId = '-1'
        this.getFolders()
        Bus.$on('openFile3', this.linkPath)
        Bus.$on('event_delFolder', this.forPath)
        Bus.$on('event_modifyPath', this.forPathRename)
        Bus.$on('Event_leftTreeCreated', this.leftTreeCreated)
    },
    mounted () {
        document.addEventListener('click', this.hiddenMenu)
    },
    destroyed () {
        Bus.$off('openFile3')
        Bus.$off('event_delFolder')
        Bus.$off('event_modifyPath')
        Bus.$off('Event_leftTreeCreated')
    },
    watch: {
        // isActive (val) {
        //     if (val) {
        //         this.setData(this.allList)
        //     }
        // }
    },
    methods: {
        operCacheData (data) { // 
            let parentId = data.folderId ? data.folderId : '/'
            data = data.hasOwnProperty('children') ? data.children : data
            for (let i = 0; i < data.length; i++) {
                if (data[i].children) {
                    this.cacheData[data[i].folderId] = {
                        val: data[i].children,
                        name: data[i].name ? data[i] : '/',
                        parentId: parentId
                    }
                    if (data[i].children.length > 0) {
                        this.operCacheData(data[i])
                    }
                }
            }
        },
        linkPath (data) {
            if (data.folderId === '/') {
                this.listData = this.dataOperation(this.allList)
                this.paths = [{
                    name: '../',
                    folderId: '/'
                }]
                Bus.$emit('Event_treeByTarget', data)
                // Bus.$emit('event_dblClick', data)
            } else {
                this.listData = this.dataOperation(this.cacheData[data.folderId].val)
                let arr = []
                this.changePath(data.folderId, arr)
                this.paths = arr
                Bus.$emit('Event_treeByTarget', data)
            }
        },
        changePath (parentId, arr) {
            if (parentId === '/') {
                arr.unshift({
                    name: '../',
                    folderId: '/'
                })
            } else {
                arr.unshift({
                    name: this.cacheData[parentId].name.name,
                    folderId: parentId
                })
                this.changePath(this.cacheData[parentId].parentId, arr)
            }
        },
        forPath (folderid) {
            this.file = ''
            this.parId = ''
            this.parData = null
            this.file = 'delete'
            this.parId = folderid
            this.getArrayList(this.allList)
            for (let i = 0; i < this.paths.length; i++) {
                if (folderid === this.paths[i].folderId) {
                    this.linkPath(this.paths[i - 1])
                }
            }
        },
        leftTreeCreated (data, parentId) {
            // this.cacheData[data.folderId] = {
            //     val: data.children,
            //     name: data.name ? data : '/',
            //     parentId: parentId
            // }
            this.file = ''
            this.parId = ''
            this.parData = null
            this.file = 'file'
            this.parId = parentId
            this.parData = data
            this.getArrayList(this.allList)
        },
        getArrayList (data) { // 
        // data: 
        // debugger;
            if (this.file == 'file') {
                for (let i in data) { // 
                    if (data[i].folderId === this.parId) {
                        // data[i].children.push(this.parData)
                        return
                    } else {
                        this.getArrayList(data[i].children)
                    }
                }
            } else if (this.file == 'delete') {
                for (let j in data) { // 
                    if (data[j].folderId === this.parId) {
                        data.splice(j, 1)
                        return
                    } else {
                        this.getArrayList(data[j].children)
                    }
                }
            } else if (this.file == 'amend') {
                for (let k in data) { // 
                    if (data[k].folderId === this.parId) {
                        data[k].name = this.parData.name
                        data[k].folderId = this.parData.folderId
                        return
                    } else {
                        this.getArrayList(data[k].children)
                    }
                }
            }
        },
        forPathRename (rename, folderid, resData) {
            // debugger;
            this.file = ''
            this.parId = ''
            this.parData = null
            this.file = 'amend'
            this.parId = folderid
            this.parData = resData
            this.getArrayList(this.allList)
            // 
            for (let i = 0; i < this.paths.length; i++) {
                if (folderid === this.paths[i].folderId) {
                    this.paths[i].name = rename
                    this.paths[i].folderId = resData.folderId
                }
            }
            this.cacheData[folderid] = this.cacheData[resData.folderId]
            this.operCacheData(this.allList)
            // this.cacheData[folderid].name = rename
            // this.cacheData[folderid].name.folderId = resData.folderid
        },
        xun (data) {
            for (let i = 0; i < data.length; i++) {
                if (data[i].children[0]) {

                }
            }
        },
        dataOperation (data) {
            this.$nextTick(() => {
                data.map((list) => {
                    list.isActive = false
                    list.focus = false
                })
            })
            // this.sortTime(data)
            data.map((item) => {
                item.createTimeTrans = Validators.formatDate(new Date(item.createTime))
                if (item.isFile) {
                    item.size = `${Math.ceil(item.lenght)} KB`
                } else {
                    item.size = ''
                }
            })
            return data
        },
        // sortTime (data) {
        //     data.sort((a, b) => {
        //         let aTime = a.createTime
        //         let bTime = b.createTime
        //         return bTime - aTime
        //     })
        // },
        freshTableData () {
            this.listData.push([])
            this.listData.pop()
        },
        noteOperIndex (index) {
            this.opFolderId = index
        },
        getFolders () { // 
            // 
            let oData = {
                spaceId: 'shareSpace'
            }
            this.listData = []
            this.http.shareGetFolders(oData).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.allList = res.data
                    // this.setData(this.allList)
                    this.listData = this.dataOperation(this.allList)
                    this.operCacheData(this.allList)
                }
            }).catch(err => {
                // this.$message({type: 'error', message: err})
                return err
            })
        },
        // setData (data) {
        //     Bus.$emit('Event_getData', data)
        // },
        onIconShrink () {
            this.$store.commit('changeShareState', 0)
        },
        newFolderFn1 () {
            let aa = false
            for (let i = 0; i < this.listData.length; i++) {
                if (this.listData[i].name === '') {
                    aa = true
                    break
                }
            }
            if (aa) {
                return this.newFolderFn2(2)
            } else {
                return ''
            }
        },
        newFolderFn2 (arg) {
            let bb = false
            for (let i = 0; i < this.listData.length; i++) {
                if (this.listData[i].name === `(${arg})`) {
                    bb = true
                    break
                }
            }
            if (bb) {
                arg++
                return this.newFolderFn2(arg)
            } else {
                return `(${arg})`
            }
        },
        onNewFolder () { // 
            let data = {
                icon: 'http://172.19.1.200:5000/icon_file_mark/folder.png',
                isFile: false,
                name: this.newFolderFn1(),
                lenght: 1,
                isDisabled: true,
                focus: true,
                isActive: false,
                index: this.listData.length
            }
            // this.listData.push(data)
            this.listData.push(data)
            if (this.shareState === 0) {
                this.onChoosed2(data)
            } else if (this.shareState === 1) {

            }
            this.$nextTick(() => {
                if (this.shareState) {
                    this.$refs['input' + (this.listData.length - 1)][0].select('selectAll')
                } else {
                    this.$refs['input' + (this.listData.length - 1)].select('selectAll')
                }
            }, 0)
        },
        onBlur (index) {
            if (!this.listData[index].folderId) { // 
                this.listData[index].isDisabled = false
                this.listData[index].focus = false
                this.createNewFolder(index)
            } else {
                let isIptChange = false
                if (this.listData[index].name != this.reNameVal) {
                    isIptChange = true
                }
                if (isIptChange) {
                    // 
                    for (let i = 0; i < this.listData.length; i++) {
                        if (this.listData[i].name == this.listData[index].name && i != index) {
                            this.$message({type: 'error', message: ''})
                            this.listData[index].name = this.reNameVal
                            this.listData[index].isDisabled = false
                            this.listData[index].focus = false
                            return
                        }
                    }

                    this.listData[index].isDisabled = false
                    this.listData[index].focus = false
                    this.renameFolder(index)
                    this.isIptChange = false
                } else {
                    this.listData[index].isDisabled = false
                    this.listData[index].focus = false
                }
                this.freshTableData()
                Bus.$emit('Event_ad_data', this.listData[index])
            }
            // this.$forceUpdate()
        },
        createNewFolder (index) { // 
            // debugger;
            // this.newFolderListData = this.listData
            // this.isFirstCreate = true
            let params = {
                'spaceId': 'shareSpace',
                // 'parentId': '/',
                'parentId': this.getPathUrl(),
                'folderName': this.listData[this.listData.length - 1].name,
                'isFile': 'false'
                // "fileContent":""
            }
            this.http.shareCreateFile(params).then(res => {
                if (res.status === this.GLOBAL.success) {
                    let data = res.data
                    data.createTimeTrans = Validators.formatDate(new Date(data.createTime))
                    data.size = ''
                    this.listData[index] = data
                    let parentId = this.getPathUrl()
                    this.operCacheData(this.allList)
                    Bus.$emit('Event_File_data', res.data, this.paths)
                    // this.cacheData[this.newFolderListData[index].folderId] = {
                    //     val: this.newFolderListData[index].children,
                    //     name: this.newFolderListData[index].name ? this.newFolderListData[index] : '/',
                    //     parentId: parentId
                    // }

                    this.freshTableData()
                }
            }).catch(err => {
                // this.listData.pop()
                this.newFolderListData.pop()
                return err
            })
        },
        renameFolder (index) { // 
            let params = {
                'spaceId': 'shareSpace',
                'folderId': this.listData[index].folderId,
                'newName': this.listData[index].name
            }
            this.http.shareRenameFile(params).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.listData[index] = res.data
                    this.operCacheData(this.allList)
                    this.freshTableData()
                }
            }).catch(err => {
                // this.$message({type: 'error', message: err})
                return err
            })
        },
        tableRowClassName ({row, rowIndex}) {
            row.index = rowIndex
            return row.isActive ? 'active' : ''
        },
        onChoosed (index, item) { // 
            this.listData.map((i) => {
                i.isActive = false
            })
            this.listData[index].isActive = true
            this.freshTableData()
            this.noteOperIndex(index)
        },
        onChoosed2 (row) { // 
            this.listData.map((i) => {
                i.isActive = false
            })
            if (this.listData[row.index]) {
                this.listData[row.index].isActive = true
            }
            this.freshTableData()
            this.noteOperIndex(row.index)
        },
        onSearch () { // 
            let params = {
                'spaceId': 'shareSpace',
                'folderId': this.getPathUrl(),
                'searchWord': this.iptSearch
            }
            this.http.shareSearchFile(params).then(res => {
                if (res.status === this.GLOBAL.success) {
                    let data = res.data
                    data.map((item) => {
                        item.createTimeTrans = Validators.formatDate(new Date(item.createTime))
                        if (item.isFile) {
                            item.size = `${Math.ceil(item.lenght)} KB`
                        } else {
                            item.size = ''
                        }
                    })
                    this.listData = data
                }
            }).catch(err => {
                // this.$message({type: 'error', message: err})
                return err
            })
        },
        onRClick (index) { // 
            if (event.button === 2) {
                this.showMenu = index
                this.computeOffset(index)
            }
        },
        onRClickRow (row, column) {
            this.showMenu = row.index
            this.computeOffset(row.index)
        },
        onDblClickRow (row, event) { // 
            if (row.hasOwnProperty('children')) {
                this.noteOperIndex('-1')
                Bus.$emit('Event_treeByTarget', row)
                this.listData = this.dataOperation(row.children)
                this.paths.push({
                    name: row.name,
                    folderId: row.folderId
                })
            }
        },
        computeOffset (index) { // 
            let cal_menu_height = function (menu_num) {
                let height = menu_num * 20 + 2 * 5
                return height
            }
            // 
            let need_menu_move_up = function () {
                let need = false
                let dy = window.innerHeight - event.screenY
                if (dy < menu_height) {
                    need = true
                }
                return need
            }
            // 
            let move_height = 0
            let menu_num = 0
            let menu_el = document.getElementById('shareRM' + index)
            if (menu_el != null && menu_el !== undefined) {
                for (let i = 0; i < menu_el.childNodes.length; i++) {
                    if (menu_el.childNodes[i].childElementCount !== 0) {
                        menu_num += 1
                    }
                }
            }
            if (menu_num === 0) {
                this.showMenu = 0
            }
            let menu_height = cal_menu_height(menu_num)
            if (need_menu_move_up()) {
                move_height = menu_height
            }
            // x
            document.getElementById('shareRM' + index).style.left =
                    event.clientX + 6 + 'px'
            // y
            document.getElementById('shareRM' + index).style.top =
                    event.clientY - move_height + 'px'
        },
        onLiClick (i, index, row) { // li
            if (i === 0) {
                this.reNameVal = row.name
                this.listData[index].isDisabled = true
                this.listData[index].focus = true
                setTimeout(() => {
                    if (this.shareState) {
                        this.$refs['input' + index][0].select('selectAll')
                    } else {
                        this.$refs['input' + index].select('selectAll')
                    }
                }, 0)
                this.onChoosed(index)
            } else if (i === 1) {
                this.dialogDelete = true
                this.deleteName = row.name
                this.deleteIndex = index
            } else if (i === 2) {
                this.isEmpty = true
                if (row.isFile && row.lenght > 0) {
                    let folderid = encodeURI(this.listData[index].folderId)
                    window.open(`${this.http.shareUrl}/downloadFileOrZip?spaceId=shareSpace&folderId=${folderid}`, '_self')
                } else if (!row.isFile) {
                    this.getDownloadIsEmpty(row.children)
                    if (!this.isEmpty) {
                        let folderid = encodeURI(this.listData[index].folderId)
                        window.open(`${this.http.shareUrl}/downloadFileOrZip?spaceId=shareSpace&folderId=${folderid}`, '_self')
                    } else {
                        this.$message({type: 'warning', message: ''})
                    }
                } else {
                    this.$message({type: 'warning', message: ''})
                }
            }
        },
        getDownloadIsEmpty (data) {
            for (let i = 0; i < data.length; i++) {
                if (data[i].isFile && data[i].lenght > 0) {
                    this.isEmpty = false
                    return
                } else {
                    this.getDownloadIsEmpty(data[i].children)
                }
            }
        },
        onConfirmDel () { // 
            this.deleteFile(this.deleteIndex)
        },
        deleteFile (index) { // 
            let paramsD = {
                'spaceId': 'shareSpace',
                'folderId': this.listData[index].folderId
            }
            this.http.shareDeleteFile(paramsD).then(res => {
                if (res.status === this.GLOBAL.success) {
                    this.listData.splice(index, 1)
                    this.showMenu = null
                    this.dialogDelete = false
                    this.deleteIndex = -1
                    Bus.$emit('Event_treeDeleteData', paramsD.folderId)
                }
            }).catch(err => {
                // this.$message({type: 'error', message: err})
                return err
            })
        },
        hiddenMenu () {
            this.showMenu = -1
        },
        getPathUrl () { // 
            return this.paths[this.paths.length - 1].folderId
        },
        uploadFileRequest (data) {
            data.headers.spaceId = 'shareSpace'
            data.headers.parentId = encodeURI(this.getPathUrl())
            data.headers.fileName = encodeURI(data.file.name)
            if (data.file.size === 0) {
                this.$message({type: 'warning', message: ''})
                return
            }
            let headers = {
                'spaceId': data.headers.spaceId,
                'parentId': data.headers.parentId,
                'fileName': data.headers.fileName
            }
            let params = new FormData()
            params.append('file', data.file)

            this.http.shareUploadFile(headers, params).then(res => {
                if (res.status === this.GLOBAL.success) {
                    let data = res.data
                    data.createTimeTrans = Validators.formatDate(new Date(data.createTime))
                    data.size = `${Math.ceil(data.lenght)} KB`
                    this.listData.unshift(data)
                    Bus.$emit('event_tree')
                }
            }).catch(err => {
                // this.$message({type: 'error', message: err})
                return err
            })
        },
        uploadFileBody () {
        },
        beforeUpload (file) { // 
            if (file.size > 1024 * 1024 * 10) {
                this.$message({type: 'warning', message: '10M'})
                return false
            }
        },
        uploadIconSuccess (res, file, fileList) { // 
            // let temp = {
            //     name: res.result[0].fileName,
            //     url: res.result[0].fileUrl,
            //     size: res.result[0].fileSize
            // }
            // this.fileList.push(temp)
            // this.getFolders()
        },
        uploadIconError (errInfo, file, fileList) { // 
            this.$message({type: 'errInfo', message: ''})
        }
    }
}
</script>

<style lang="scss" scoped>
    #share{
        height: 100%;
        width: 100%;
        color: #fff;
        text-align: left;
        background-color: #1D1F20;
        box-sizing: border-box;
        border-left: 1px solid #4D4D4D;
        .path {
            padding-left: 15px;
            font-size: 16px;
            color: #ccc;
            height: 35px;
            line-height: 35px;
            border-top: 1px solid #4D4D4D;
            border-bottom: 1px solid #4D4D4D;
            box-sizing: border-box;
            &>img {
                display:inline-block;
                width:20px;
                height:17px;
                vertical-align: middle;
            }
        }
        .folderCont {
            padding: 20px 15px 0;
            height: calc(100% - 35px);
            box-sizing: border-box;
            .folderFunction {
                &:after {
                    content: "";
                    display: block;
                    height: 0;
                    clear:both;
                    visibility:hidden;
                }
                .folderOperate {
                    float: left;
                }
                .folderSearch {
                    float: right;
                }
                .el-button {
                    padding: 11px 20px;
                    background: #0E639C;
                    border-color: #0E639C;
                    color: #fff;
                    font-size: 16px;
                    border-radius: 2px;
                    box-sizing: border-box;
                }
                .icon-new,.icon-file,.icon-folder {
                    display: inline-block;
                }
                .icon-new {
                    position: relative;
                    top: 2px;
                    width: 17px;
                    height: 16px;
                    margin-top: -3px;
                    margin-right: 2px;
                    background: url('../../assets/img/newFile.png');
                }
                .icon-file {
                    position: relative;
                    top: 3px;
                    width: 16px;
                    height: 17px;
                    margin-top: -4px;
                    margin-right: 5px;
                    background: url('../../assets/img/lead.png');
                }
                .icon-folder {
                    position: relative;
                    top: 3px;
                    width: 19px;
                    height: 16px;
                    margin-top: -3px;
                    margin-right: 5px;
                    background: url('../../assets/img/leadClick.png');
                }
                .folderSearch {
                    .el-input {
                        width: 350px;
                        &>>>.el-button {
                            background: transparent;
                            border-width: 0;
                            padding: 11px 10px;
                        }
                    }
                    &>>>.el-input__inner {
                        padding-right: 40px;
                        color: #ccc;
                        background-color: #3C3C3C;
                        border-color: #1D1F20;
                        box-sizing: border-box;
                        &:focus {
                            border-color: #0E639C;
                        }
                    }
                    .icon-shrink,.icon-list {
                        display: inline-block;
                        width: 26px;
                        height: 26px;
                        vertical-align: middle;
                        margin-left: 10px;
                        cursor: pointer;
                    }
                    .icon-shrink {
                        background: url('../../assets/img/thumbnail.png') no-repeat;
                    }
                    .icon-list {
                        background: url('../../assets/img/tableVie.png') no-repeat;
                    }
                }
                .upload-file {
                    display: inline-block;
                    &>>>.el-upload-list {
                        display: none!important;
                    }
                }
            }
            .folder {
                height: calc(100% - 40px);
                .itemBox {
                    height: 100%;
                    list-style: none;
                    margin: 0;
                    box-sizing: border-box;
                    overflow-y: scroll;
                    overflow-x: hidden;
                    &:after {
                        content: "";
                        display: block;
                        height: 0;
                        clear:both;
                        visibility:hidden;
                    }
                    .item {
                        position: relative;
                        height: auto;
                        list-style: none;
                        color: #ccc;
                        width: 122px;
                        height: 127px;
                        padding: 20px 0 0;
                        box-sizing: border-box;
                    }
                }
                .border_input{
                    &>>>.el-input__inner{
                        border-color: #0E639C;
                    }
                }
                .el-input {
                    font-size: 15px;
                }
                &>>>.el-input__inner {
                    padding: 0;
                    color: #ccc;
                    background: transparent;
                    height: 30px;
                    line-height: 30px;
                    border-color: transparent;
                    white-space:nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                }
                &>>>.el-input.is-disabled .el-input__inner {
                    cursor: default;
                }
                &.shrink {
                    &>>>.el-input__inner {
                        text-align: center;
                    }
                    .itemBox {
                        padding: 20px 0 0;
                        .item {
                            text-align: center;
                            float: left;
                            .icon-item {
                                display: inline-block;
                                width: 100%;
                                &>img {
                                    height: 60px;
                                    display: inline-block;
                                }
                            }
                            .name-item {
                                margin: 0;
                                text-align: center;
                                line-height: 30px;
                                font-size: 16px;
                                margin-top: 5px;
                                padding: 0 5px;
                            }
                            &:hover,&.active {
                                background:rgba(46,45,45,1);
                                border-radius:8px;
                            }
                        }
                    }
                }
                &.list {
                    &>>>.el-input__inner {
                        width: calc(100% - 40px) ;
                    }
                    &>>>.el-table {
                        color: #ccc;
                        &:before {
                            height: 0;
                        }
                    }
                    &>>>.el-table, .el-table__expanded-cell {
                        background-color: #1d1f20;
                    }
                    &>>>.el-table th, &>>>.el-table tr {
                        background-color: #1d1f20;
                    }
                    &>>>.el-table tr.active {
                        background:rgba(46,45,45,1);
                    }
                    &>>>.el-table--enable-row-hover .el-table__body tr:hover>td {
                        // background-color: #1d1f20;
                        background:rgba(46,45,45,1);
                    }
                    &>>>.el-table td, &>>>.el-table th {
                        padding: 8px 0;
                    }
                    &>>>.el-table td, &>>>.el-table th.is-leaf {
                        border-bottom: 1px solid #333;
                    }
                }
            }
            .item-mask {
                position: absolute;
                background:rgba(37,37,37,1);
                box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 1);
                border-radius:2px;
                z-index:9;
                list-style: none;
                padding: 5px 10px;
                min-width: 200px;
                max-width: 300px;
                display: none;
                .mask-list {
                    text-align: left;
                    font-size: 14px;
                    color: #ccc;
                    height: 24px;
                    line-height: 24px;
                    white-space:nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    .mask-title,.mask-cont {
                        display: inline-block;
                    }
                }
            }
            .menu {
                display: inline-block;
                position: fixed;
                box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 1);
                padding: 0px;
                list-style-type: none;
                background-color: #252525;
                z-index: 99;
                li {
                    // padding:5px 60px 5px 15px;
                    padding: 0 60px 0 15px;
                    line-height: 30px;
                    color: #fff;
                    text-align:left;
                    a {
                        color: #fff;
                        text-decoration: none;
                    }
                }
                li:hover {
                    background-color: #0E639C;
                }
            }
            .tableMask {
                position:relative
            }
            .item,.tableMask {
                &:hover {
                    .item-mask {
                        display:block;
                    }
                }
            }
        }
        .listTable {
            .listImg {
                display: inline-block;
                width: 35px;
                text-align: center;
                vertical-align: middle;
                overflow: hidden;
                &>img {
                    height: 18px;
                    display: inline-block;
                    vertical-align: middle;
                }
            }
            .listName {
                display: inline-block;
                width: calc(100% - 40px);
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
                vertical-align: middle;
            }
        }
    }
    .el-tooltip__popper {
        max-width: 20px;
        width: 20px;
    }
    ::-webkit-scrollbar {/**/
        width: 10px;     /**/
        height: 1px;
    }
    ::-webkit-scrollbar-thumb {/**/
        border-radius: 10px;
        background: #535353;
    }
    ::-webkit-scrollbar-track {/**/
        border-radius: 10px;
        background: #2C2E2F;
    }
    // 
    >>> .el-table__body-wrapper::-webkit-scrollbar {
    width: 10px;     /**/
    height: 1px;
    }
    // 
    >>> .el-table__body-wrapper::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: #535353;
    }
    >>> .el-table__body-wrapper::-webkit-scrollbar-track {/**/
        border-radius: 10px;
        background: #2C2E2F;
    }
    >>>.dialogStyle{
        background:rgba(37,37,37,1);
        box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 0.5);
        border-radius:4px;
        .el-dialog__header{
            text-align: left;
            .el-dialog__title{
                color: #CCCCCC;
            }
        }
        .el-dialog__body {
            text-align: center;
            color: #ccc;
            font-size: 14px;
        }
        .el-dialog__footer {
            text-align: center;
            .el-button {
                color: #fff;
                background:rgba(14,99,156,1);
                border-color:rgba(14,99,156,1);
                &.el-button--default {
                    color: #080808;
                    background:rgba(237,240,242,1);
                    border-color:rgba(237,240,242,1);
                }
            }
        }
    }
</style>



