<!---->
<template>
    <section id="aloneFileDebug" @mousemove="changeLogHeight" @mouseleave="mouseLeave">
        <div class="code" id="code1" ref="code">
            <div class="top">
                <div class="top1">
                    <div class="text">
                        <span style="white-space: nowrap;">HTMLJS</span>
                        <!-- <span style="white-space: nowrap;">{{codeName}}</span> -->
                    </div>
                    <div class="action">
                        <!-- <img class="run" :src="require('@/assets/img/runClick.png')" v-if="runActive" @click="endScript"> -->
                        <img class="run" :src="runimg" @click="runScript" @mouseover="runimg=runimg2" @mouseleave="runimg=runimg1" title="">
                        <img class="save" :src="require('@/assets/img/saveClick.png')" v-if="saveActive" @click="saveScript" title="">
                        <img class="save" :src="require('@/assets/img/save.png')" v-else @click="notSave" title="">
                    </div>
                </div>
                <!-- {{activeFile}} -->
                <el-tabs v-model="activeFile" type="card" @edit="handleTabsEdit" closable>
                    <el-tab-pane v-for="item in codeData" :label="item.name" :key="item.folderId" :name="item.folderId">
                    </el-tab-pane>
                </el-tabs>
            </div>
            <div class="codeBox">
                <codemirror v-show="item.folderId===activeFile" v-for="(item, index) in codeData" :key="item.folderId" :idx="index"></codemirror>
                <!-- <codemirror></codemirror> -->
            </div>
            <div class="runMenu" v-if="runActive">
                <div v-for="(item, index) in codeName" :key="index" class="runFile">
                    <el-row>
                        <div class="runFileBox" @click="runFile(item, index)">
                            <!-- {{codeData[index].icon}} -->
                            <div style="width:15%"><img :src="codeData[index].icon"></div>
                            <div style="width:85%" class="title"><span>{{item}}</span></div>
                        </div>
                    </el-row>
                </div>
            </div>
            <div class="runMenu" v-if="saveActive">
                <div class="runFile">
                    <el-row>
                        <div class="runFileBox" @click="save(0)">
                            <div><span></span></div>
                        </div>
                        <div class="runFileBox" @click="save(1)">
                            <div><span></span></div>
                        </div>
                    </el-row>
                </div>
            </div>
        </div>
        <log class="log" ref="log"></log>
    </section>
</template>

<script>
import 'codemirror/theme/ambiance.css'
import 'codemirror/lib/codemirror.css'
import 'codemirror/addon/hint/show-hint.css'
import log from '@/components/log'
import Bus from '@/bus'
import {mapState} from 'vuex'
import codemirror from '@/components/codemirror'
export default {
    name: 'aloneFileDebug',
    components: {
        log,
        codemirror
    },
    data () {
        return {
            editor: null,
            codes: 'echo 1',
            taskContent: '',
            dayProjectName: 'zz',
            dayJobName: 'test1',
            item: {
                code: ''
            },
            // 
            runimg: {},
            runimg1: require('@/assets/img/run.png'),
            runimg2: require('@/assets/img/runClick.png'),
            proJect: '',
            jobNames: '',
            // 
            activeFile: '-1',
            runActive: false,
            saveActive: false,
            listenFn: {},
            // 
            selectFileData: {}
        }
    },
    watch: {
        taskContent: {
            immediate: true,
            handler: function () {
                if (this.editor) {
                    this.stakContent()
                }
            }
        },
        codes: {
            immediate: true,
            handler: function () {
                if (this.editor) {
                    // this.editor.setValue(this.codes)
                }
            }
        },
        activeFile (newValue, oldValue) {
            Bus.$emit('page1Select', newValue)
        },
        dayProjectName () {
            this.proJect = this.dayProjectName
        },
        dayJobName () {
            this.jobNames = this.dayJobName
        }
    },
    computed: {
        ...mapState(['codeName', 'codeData', 'codeFolderId', 'codeFileSave', 'codeFile'])
    },
    created () {
        this.runimg = this.runimg1
        // 
        Bus.$on('openFile1', (data) => {
            this.$store.commit('setcodeData', data)
            this.activeFile = data.folderId
        })
        Bus.$on('fileRename', () => {
            if (this.codeFolderId.length === 0) {
                this.activeFile = '-1'
                return false
            }
            for (let i = 0; i < this.codeFolderId.length; i++) {
                if (this.codeFolderId[i] === this.activeFile) {
                    this.activeFile = '-1'
                    return false
                }
            }
        })
        // run
        let that = this
        this.listenFn = function setRunActive (e) {
            if (e.target.className !== 'save' && e.target.className !== 'runFileBox') {
                // console.log(e.target.className)
                that.saveActive = false
            }
        }
        document.addEventListener('click', this.listenFn)
    },
    mounted () {
    },
    methods: {
        stakContent () {
            var params = {
                projectName: this.dayProjectName,
                jobName: this.dayJobName,
                jobContent: this.editor.getValue()
            }
            // console.log(params)
        },
        // 
        handleTabsEdit (targetName, action) {
            this.$store.commit('removeCode', targetName)
            if (this.activeFile === targetName) {
                if (this.codeFolderId[0]) {
                    this.activeFile = this.codeFolderId[0]
                }
            }
        },
        runScript () {
            let folderId = this.activeFile
            let index = this.codeFolderId.indexOf(folderId)
            if (folderId !== -1) {
                this.runFile(folderId, index)
            }
        },
        endScript () {
            this.runActive = false
        },
        saveScript () {
            this.saveActive = false
        },
        notSave () {
            this.saveActive = true
        },
        save (type) {
            // 0
            // 1
            this.saveActive = false
            let oData = {}
            if (type === 0) {
                let idx = this.codeFolderId.indexOf(this.activeFile)
                oData = {
                    spaceId: 'debug',
                    folderId: '',
                    change: {}
                }
                oData.change[this.codeData[idx].folderId] = this.codeFile[this.codeData[idx].folderId]
                this.http.runHtmlPage(oData).then(res => {
                    if (res.status === 200) {
                        let temp = {
                            folderId: this.codeData[idx].folderId,
                            data: this.codeFile[this.codeData[idx].folderId]
                        }
                        this.$store.commit('setCodeFileSave', temp)
                        this.$message({type: 'success', message: ''})
                    }
                }).catch(err => {
                    return err
                    // this.$message({type: 'error', message: err})
                })
            } else if (type === 1) {
                oData = {
                    spaceId: 'debug',
                    folderId: '',
                    change: this.codeFile
                }
                this.http.runHtmlPage(oData).then(res => {
                    if (res.status === 200) {
                        this.$store.commit('setCodeFileSave2')
                        this.$message({type: 'success', message: ''})
                    }
                }).catch(err => {
                    return err
                })
            }
        },
        fileShow (name) {
            return false
        },
        // 
        runFile (name, index) {
            // vuex
            if (index === -1) {
                return false
            }
            let folderID = this.codeData[index].folderId
            name = this.codeData[index].name
            // let fileContent = this.codeFile[folderID]
            // 
            let fileTypeArr = name.split('.')
            let fileType = fileTypeArr[fileTypeArr.length - 1]
            if (fileType === 'html' || fileType === 'xml') {
                let oData = {
                    spaceId: 'debug',
                    folderId: folderID,
                    change: this.codeFile
                }
                this.http.runHtmlPage(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$message({type: 'success', message: 'HTML'})
                        let temp = {
                            folderId: folderID,
                            data: this.codeFile[folderID]
                        }
                        this.$store.commit('setCodeFileSave', temp)
                    }
                }).catch(err => {
                    return err
                })
            } else if (fileType === 'js') {
                let oData = {
                    spaceId: 'debug',
                    folderId: folderID,
                    change: this.codeFile
                }
                this.http.runJsCode(oData).then(res => {
                    if (res.status === this.GLOBAL.success) {
                        this.$message({type: 'success', message: 'js'})
                        let temp = {
                            folderId: folderID,
                            data: this.codeFile[folderID]
                        }
                        this.$store.commit('setCodeFileSave', temp)
                    }
                }).catch(err => {
                    return err
                })
            }
        },
        changeLogHeight () {
            // console.log(window.event.clientY)
            if (this.$refs.log.changeHeightClicked) {
                this.$refs.log.changeHeight(window.event.clientY)
            }
        },
        mouseLeave () {
            this.$refs.log.changeHeightClicked = false
        }
    },
    destroyed () {
        // let that = this
        document.removeEventListener('click', this.listenFn, false)
        Bus.$off('openFile1')
        Bus.$off('fileRename')
    }
}
</script>

<style lang="scss" scoped>
#aloneFileDebug{
    overflow: hidden;
    height: 100%;
    width: 100%;
    color: #fff;
    text-align: left;
    position: relative;
    .top{
        height: 70px;
        background-color: #252525;
        border:1px solid rgba(77,77,77,1);
        .top1{
            height: 35px;
            font-size: 16px;
            border-bottom: 1px solid rgba(77,77,77,1);
            color: #ccc;
            position: relative;
            .text{
                width: calc(100% - 120px);
                overflow: hidden;
                text-overflow:ellipsis;
                white-space:nowrap;
                word-break:keep-all;
            }
            span{
                line-height: 35px;
                padding-left: 10px;
            }
            .run{
                position: absolute;
                right: 80px;
                top: 7px;
            }
            .run:hover{

            }
            .save{
                position: absolute;
                right: 20px;
                top: 7px;
            }
        }
        .top2{
            height: 35px;
        }
    }
    #code1{
        height: calc(100% - 300px);
    }
    .log{
        height:300px;
    }
    .tab{
        // .icon{
        //     width: 30%;
        // }
        // .name{
        //     width: 70%;
        // }
    }
    .codeBox{
        width: 100%;
        height: calc(100% - 71px);
        background-color: #1D1F20;
        border-left: 1px solid #4d4d4d;
        border-right: 1px solid #4d4d4d;
        position: relative;
    }
}
.runMenu{
    position: absolute;
    width: 200px;
    z-index: 5;
    top: 75px;
    right: 0;
    background:rgba(37,37,37,1);
    box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 1);
    .runFile{
        // line-height: 30px;
        // padding: 0 10px;
        // height: 30px;
        .title{
            overflow:hidden;
            text-overflow:ellipsis;
            white-space:nowrap;
            word-break:keep-all;
        }
        .runFileBox{
            display: flex;
            height: 30px;
            line-height: 30px;
            padding: 0 10px;
            cursor: pointer;
            // background-color: #fff;
        }
        .runFileBox:hover{
            background-color: #555;
        }
    }
}
</style>

<style lang="scss">
.top{
    .el-tabs--card>.el-tabs__header{
        border-bottom:none;
    }
    .el-tabs__nav{
        border: none !important;
    }
    .el-tabs__item{
        color: #fff !important;
        background-color: #2f2f2f;
        line-height: 34px;
        height: 34px;
    }
    .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
        border-bottom-color: red;
    }
    .el-tabs--card>.el-tabs__header .el-tabs__item {
        border-left: 1px solid #4D4D4D;
    }
}
.action{

}
.el-tabs__item.is-top:nth-child(2) {
    // padding-left: 20px !important;
}
.el-tabs--top.el-tabs--card>.el-tabs__header .el-tabs__item:nth-child(2) {
    // padding-left: initial !important;
}
</style>



