  <template>
  <div class="tree">
    <!-- {{treeData}} -->
    <el-tree
      id="TreeMenu"
      ref="tree"
      node-key="folderId"
      :show-checkbox="treeCheckBox"
      :default-expand-all="treeExpandAll"
      :expand-on-click-node="false"
      :data="treeData"
      :props="defaultProps"
      :auto-expand-parent="true"
      :default-checked-keys="treeKeysId"
      @check="treeCheckNodes"
      @node-expand="expandFn"
      @node-collapse="hiddenMenu"
      @node-contextmenu="rClickNode"
      @node-click="rightMenuClose1"
    >
      <div :class="['custom-tree-node',data.isFile?'nodeFolder-hide':'nodeFolder-show']" slot-scope="{ node, data }">
        <!-- <img
          :src="data.icon.replace('img','static/img')"
          alt
          style="vertical-align: middle;"
          v-if="data.icon"
        /> -->
        <!-- <img v-if="data.icon" :src="require('@/assets/img/' + data.icon)" alt style="vertical-align: middle;"/> -->
        <!-- <img v-if="false">
        <img v-else :src="require('@/assets/img/' + common_icon)" alt style="vertical-align: middle;" /> -->
        <img :src="data.icon" alt style="vertical-align: middle;max-height: 21px;" />
        <span
          v-show="showRename !== (data.folderId + selectPage)  && data.folderId !== 999"
          class="node"
          :class="clickBlueDag == data.folderId?'click-blue':''"
          @dblclick="handleNodeClick(data)"
          @click="rightMenuClose"
        >{{data.name}}</span>
        <input
          v-show="showRename === (data.folderId + selectPage)"
          type="text"
          v-model="inputRename"
          @keyup.enter="$event.target.blur"
          @blur.prevent="confirmRename(node,data)"
          class="input"
          autofocus
        />
        <input
          v-show="data.folderId === 999"
          type="text"
          v-model="inputNewFile"
          @keyup.enter="$event.target.blur"
          @blur.prevent="confirmNewfile(node,data)"
          class="input"
          autofocus
        />
        <ul :id="'Menu'+ node.id" v-show="showMenu == node.id" class="menu">
             <!-- <img :src="('../../static/img/css.png').replace('img','static/image')" alt="" style="vertical-align: middle;" v-if="data.icon">
            <img :src="common_icon" alt="" style="vertical-align: middle;"> -->
            <!--treeMenu.js-->
            <!-- 111 -->
                <li  v-for="(item,index) in menuList" :key="index"  @click="item.click(node, data)">
                    <div v-if="transJs(item.condition, data)">{{item.name}}</div>
                </li>
        </ul>
      </div>
    </el-tree>
    <el-dialog class="dlt-dialog" title="" :visible.sync="dialogDeleteVisible" width="400px" custom-class="beforeDelete">
        <div slot="title" style="display:flex;">
            <img style="height:20px;width:26px;" :src="require('@/assets/img/logo.png')">
            <div style="line-height:20px;margin-left:10px;"></div>
        </div>
      <span>&nbsp;{{deleteName}}&nbsp;</span>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmDlt"> </el-button>
        <el-button @click="dialogDeleteVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import menu from '@/assets/data/treeMenu3.js'
import Bus from '@/bus'
import {mapState} from 'vuex'
export default {
    props: {
        treeData: {
            type: Array,
            required: true
        },
        treeCheckBox: {
            type: Boolean,
            required: true
        },
        treeExpandAll: {
            type: Boolean,
            required: true
        },
        treeKeysId: {
            type: Array,
            required: true
        },
        pagetab: {
            type: String,
            required: true
        }
    },
    data () {
        return {
            img1: require('../assets/img/html.png'),
            menuList: [],
            defaultProps: {
                children: 'children',
                label: 'name'
            },
            common_icon: 'common.png',
            dialogDeleteVisible: false,
            showMenu: 0, // 
            showRename: 0, // 
            inputRename: '', // 
            inputNewFile: '', // 
            deleteName: '', // 
            idArr: [],
            clickBlueDag: null, // 
            newFileData: {},
            reNameData: {}
        }
    },
    computed: {
        ...mapState(['selectPage', 'nodes', 'datas'])
    },
    watch: {
        treeData (val) {
            this.onlyFolder()
        }
    },
    methods: {
        treeByTarget (data) {
            this.clickBlueDag = data.folderId
        },
        expandFn () {
            this.onlyFolder()
            this.hiddenMenu()
        },
        onlyFolder () {
            this.$nextTick(() => {
                let arr = document.getElementsByClassName('nodeFolder-hide')
                let arr2 = document.getElementsByClassName('nodeFolder-show')
                for (let i = 0; i < arr.length; i++) {
                    arr[i].parentNode.style.display = 'none'
                }
                for (let i = 0; i < arr2.length; i++) {
                    arr2[i].parentNode.style.display = 'flex'
                }
            })
        },
        transJs (conditon, data) {
            // js
            return eval(conditon)
        },
        hiddenMenu () {
            // 
            this.showMenu = 0
        },
        rightMenuClose () {
            this.showMenu = 0
            Bus.$emit('closeRightMenu')
        },
        rightMenuClose1 (node) {
            Bus.$emit('closeRightMenu')
        },
        handleNodeClick (data) {
            // 
            Bus.$emit('closeRightMenu')
            this.clickBlueDag = data.folderId
            this.$emit('treeNodeClick', data)
            Bus.$emit('openFile' + this.selectPage, data)
            this.hiddenMenu()
        },
        rClickNode (client, data, node, compself) {
            // 
            Bus.$emit('closeRightMenu')
            this.showMenu = node.id
            // 
            let cal_menu_height = function (menu_num) {
                let height = menu_num * 20 + 2 * 5
                return height
            }

            // 
            let need_menu_move_up = function () {
                let need = false
                let dy = window.innerHeight - client.screenY
                if (dy < menu_height) {
                    need = true
                }
                return need
            }
            // 
            let move_height = 0
            let menu_num = 0
            let menu_el = document.getElementById('Menu' + node.id)
            if (menu_el != null && menu_el !== undefined) {
                for (let i = 0; i < menu_el.childNodes.length; i++) {
                    if (menu_el.childNodes[i].childElementCount !== 0) {
                        menu_num += 1
                    }
                }
            }
            // 
            if (menu_num === 0) {
                this.showMenu = 0
                return
            }
            let menu_height = cal_menu_height(menu_num)
            if (need_menu_move_up()) {
                move_height = menu_height
            }
            // x
            document.getElementById('Menu' + node.id).style.left =
                event.clientX + 6 + 'px'
            // y
            document.getElementById('Menu' + node.id).style.top =
                event.clientY - move_height + 'px'
        },
        newFile (node, data) {
            // 
            this.inputNewFile = ''
            node.expanded = true
            const newChild = {
                name: 'testtest',
                children: [],
                icon: 'http://172.19.1.133:5000/icon_file_mark/folder.png',
                folderId: 999,
                isFile: false,
                spaceId: data.spaceId
            }
            data.children.push(newChild)
            this.hiddenMenu()
        },
        confirmNewfile (node, data) {
            // 
            this.$emit('treeNewfile', node, this.inputNewFile, data)
            this.newFileData = data
        },
        newfileSuccess (newdata) {
            // 
            var json = newdata
            this.newFileData.folderId = json.folderId
            this.newFileData.name = json.name
            this.newFileData.mode = json.mode
            this.newFileData.children = json.children
            this.newFileData.type = json.type
            this.newFileData.ud = json.ud
            this.newFileData.icon = json.icon
        },
        dlt (node, data) {
            // 
            this.deleteData = data
            this.dialogDeleteVisible = true
            this.deleteFolderId = data.folderId
            this.deleteName = data.name
            const parent = node.parent
            const children = parent.data.children || parent.data
            const index = children.findIndex(d => d.$treeNodeId === data.$treeNodeId)
            this.deleteChildren = children
            this.deleteIndex = index
        },
        confirmDlt () {
            // 
            this.dialogDeleteVisible = false
            this.$emit('treeDelete', this.deleteData)
        },
        dltSuccess () {
            // 
            var children = this.deleteChildren
            var index = this.deleteIndex
            children.splice(index, 1)
        },
        reName (node, data) {
            // 
            this.reNameData = data
            this.showRename = data.folderId + this.selectPage// 
            this.inputRename = data.name // 
            this.hiddenMenu() // 
        },
        confirmRename (node, data) {
            // 
            this.$emit('treeRename', data, this.inputRename)
        },
        renameSuccess (data) {
            // 
            this.reNameData = data
            this.showRename = 0
            this.$message({
                message: '',
                type: 'success'
            })
            // this.reNameData.name = data.name
            // this.reNameData.children = data.children
        },
        newTask (node, data) {
            // 
            this.inputNewFile = ''
            node.expanded = true
            const newChild = {
                name: 'testtest',
                children: [],
                icon: '',
                folderId: 999,
                isFile: true
            }
            data.children.push(newChild)
            this.hiddenMenu()
            // this.$emit('treeNewTask', node, data)
            // node.expanded = true
            // this.newTaskData = data
            // this.newFileData = data
        },
        newTaskSuccess (newData) {
            // 
            const newChild = JSON.parse(newData)
            this.newTaskData.children.push(newChild)
        },
        newAdMenu (node, data) {
            // 
            node.expanded = true
            this.$emit('treeNewAdMenu', node, data)
            const parent = node.parent
            const children = parent.data.children || parent.data
            const index = children.findIndex(d => d.$treeNodeId === data.$treeNodeId)
            this.deleteChildrens = children
            this.deleteIndexs = index
            this.childrenNode = node.parent.data
        },
        newAdMenuSuc (newData) {
            // 
            var children = this.deleteChildrens
            var index = this.deleteIndexs
            let temp = JSON.parse(newData)
            children.splice(index, 1)
            this.childrenNode.children.push(temp)
        },
        treeCheckNodes (ednode) {
            // 
            let checnode = this.$refs.tree.getCheckedNodes()
            let half = this.$refs.tree.getHalfCheckedNodes()
            this.$emit('treeCheck', ednode, checnode, half)
        },
        loadSucceed (data) {
            setTimeout(() => {
            // 
                let ztree = document.getElementById('TreeMenu')
                let ztree_vue = ztree.__vue__
                if (ztree_vue.$children.length !== 0) {
                    let ztree_vue_node = ztree_vue.$children[0].node
                    ztree_vue_node.expanded = true
                }
            }, 10)
        },
        newFolderBottom () {
            this.newFile(this.nodes, this.datas)
        },
        newFileBottom () {
            this.newTask(this.nodes, this.datas)
        },
        ceshi (node, data) {
            let temp = {
                node: node,
                data: data
            }
            this.$store.commit('setNodes', temp)
        }
    },
    created () {
        Bus.$on('event_tree', this.onlyFolder)
        // Bus.$on('event_dblClick', this.handleNodeClick)
        Bus.$on('event_expand', this.expandFn)
        Bus.$on('Event_treeByTarget', this.treeByTarget)
        // Bus.$on('fileRename', () => {
        //     let data = this.reNameDataTemp
        //     this.reNameData = data
        //     this.showRename = data.folderId + this.selectPage// 
        //     this.inputRename = data.name // 
        //     this.hiddenMenu() // 
        // })
        menu.sendThis(this)
        this.menuList = menu.dataIntegration
        if (this.selectPage === '1') {
            this.menuList = menu.dataIntegration
        } else if (this.selectPage === '2') {
            this.menuList = menu.appletData
        } else if (this.pagetab === '3') {
            this.menuList = menu.share
        }
    },
    mounted () {},
    destroyed () {
        Bus.$off('event_tree')
        // Bus.$off('event_dblClick')
        Bus.$off('event_expand')
        Bus.$off('Event_treeByTarget')
        // this.menuList = menu.aloneFileDebug
        this.menuList = menu.dataIntegration
        if (this.selectPage === '1') {
            this.menuList = menu.dataIntegration
        } else if (this.selectPage === '2') {
            this.menuList = menu.appletData
        } else if (this.pagetab === '3') {
            this.menuList = menu.share
        }
    }
}
</script>

<style lang="scss" scoped>
.dlt-dialog {
  .el-dialog__body {
    text-align: center;
    font-size: 16px;
    padding: 30px 20px;
    height: 100px;
    line-height: 40px;
  }
}
.tree {
  height: 100%;
  overflow: auto;
  .el-tree {
    height: 100%;
    // overflow: auto;
    .node {
      display: inline-block;
      max-width: 900px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 15px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      position: relative;
      top: 4px;
    }
    .click-blue {
      color: #269ee7;
    }
    .input {
      border: 1px #ddd solid;
      width: 100px;
      padding: 2px;
      font-size: 15px;
    }
    .menu {
      display: inline-block;
      position: fixed;
      box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 1);
      padding: 0px;
      list-style-type: none;
      background-color: #252525;
      z-index: 99;
      li {
        // padding:5px 60px 5px 15px;
        padding: 0 60px 0 15px;
        line-height: 30px;
        color: #fff;
        text-align:left;
      }
      li:hover {
        // color: blue;
        background-color: #0E639C;
      }
    }
  }
}
</style>

<style lang="scss">
.beforeDelete{
    background:rgba(37,37,37,1) !important;
    box-shadow:0px 0px 9px 1px rgba(0, 0, 0, 0.5) !important;
    border-radius:4px;
    .el-dialog__header{
        text-align: left;
        .el-dialog__title{
            color: #CCCCCC;
        }
    }
    .el-dialog__body{
        color: #fff;
    }
    .dialog-footer{
        text-align: center;
    }
}
</style>



